import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import {COUNTRY_LIST} from "../../shared/referenceData";

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step16(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [addType, setAddType] = useState("apt");

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'street':
            case 'apt':
            case 'ste':
            case 'floor':
            case 'city':
            case 'state':
            case 'zipCode':
            case 'province':
            case 'postCode':
                updatedState.beneficiaryInfo.currentAddress[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo.currentAddress, 'required', errors).error);
                break;
            case 'country':
                updatedState.beneficiaryInfo.currentAddress[fieldName] = value.code;
                updatedState.beneCurrAddrCountry = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo.currentAddress, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.beneficiaryInfo.currentAddress.apt;
                delete updatedState.beneficiaryInfo.currentAddress.ste;
                delete updatedState.beneficiaryInfo.currentAddress.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        errors = fieldValidator('street', state.beneficiaryInfo.currentAddress, 'required', errors).error;
        errors = fieldValidator(addType, state.beneficiaryInfo.currentAddress, 'required', errors).error;
        errors = fieldValidator('city', state.beneficiaryInfo.currentAddress, 'required', errors).error;
        errors = fieldValidator('state', state.beneficiaryInfo.currentAddress, 'required', errors).error;
        errors = fieldValidator('zipCode', state.beneficiaryInfo.currentAddress, 'required', errors).error;
        errors = fieldValidator('province', state.beneficiaryInfo.currentAddress, 'required', errors).error;
        errors = fieldValidator('postCode', state.beneficiaryInfo.currentAddress, 'required', errors).error;
        let newForm = fieldValidator('country', state.beneficiaryInfo.currentAddress, 'required', errors);

        if (newForm.isValid) {
            let updatedState = {...state};
            if (state.beneficiaryInfo.currentAddress.country === "USA") {
                updatedState.beneficiaryInfo['intendToLiveInUSA'] = "SAME";
                delete updatedState.beneficiaryInfo.physAddressOutUSA;
            } else {
                updatedState.beneficiaryInfo['physAddressOutUSA'] = "SAME";
                delete updatedState.beneficiaryInfo.intendToLiveInUSA;
            }
            props.onChangeState(updatedState);
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Where does Immigrating Relative Currently Live?`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={'10'} md={'8'}>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <MDBFormInline className={`justify-content-center`}>
                                    <MDBInput
                                        label="APT."
                                        type='checkbox'
                                        id='addTypes1'
                                        name={`addTypes1`}
                                        checked={addType === "apt"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'apt')}
                                    />
                                    <MDBInput
                                        label="Suite"
                                        type='checkbox'
                                        id='addTypes2'
                                        name={`addTypes2`}
                                        checked={addType === "ste"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'ste')}
                                    />
                                    <MDBInput
                                        label="Floor"
                                        type='checkbox'
                                        id='addTypes3'
                                        name={`addTypes3`}
                                        checked={addType === "floor"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'floor')}
                                    />
                                </MDBFormInline>
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label={types[addType].label}
                                    error={!!errorMsg[addType]}
                                    helperText={errorMsg[addType]}
                                    type="text"
                                    autoComplete="off"
                                    id={types[addType].id}
                                    name={types[addType].id}
                                    placeholder={types[addType].placeholder}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.currentAddress[addType] || ''}
                                    onChange={e => onChange(e, addType, e.target.value)}
                                    onBlur={e => onChange(e, addType, e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Street Number and Name"
                                    error={!!errorMsg.street}
                                    helperText={errorMsg.street}
                                    type="text"
                                    id={`street`}
                                    autoComplete="off"
                                    name="street"
                                    placeholder={`Street Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.currentAddress.street || ''}
                                    onChange={e => onChange(e, 'street', e.target.value)}
                                    onBlur={e => onChange(e, 'street', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="City or Town"
                                    error={!!errorMsg.city}
                                    helperText={errorMsg.city}
                                    type="text"
                                    id={`city`}
                                    autoComplete="off"
                                    name="city"
                                    placeholder={`City`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.currentAddress.city || ''}
                                    onChange={e => onChange(e, 'city', e.target.value)}
                                    onBlur={e => onChange(e, 'city', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="State"
                                    error={!!errorMsg.state}
                                    helperText={errorMsg.state}
                                    type="text"
                                    id={`state`}
                                    autoComplete="off"
                                    name="state"
                                    placeholder={`State Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.currentAddress.state || ''}
                                    onChange={e => onChange(e, 'state', e.target.value)}
                                    onBlur={e => onChange(e, 'state', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Zip Code"
                                    error={!!errorMsg.zipCode}
                                    helperText={errorMsg.zipCode}
                                    type="text"
                                    id={`zip`}
                                    name="zip"
                                    placeholder={`Zip Code`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.currentAddress.zipCode || ''}
                                    onChange={e => onChange(e, 'zipCode', e.target.value)}
                                    onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Province"
                                    error={!!errorMsg.province}
                                    helperText={errorMsg.province}
                                    type="text"
                                    id={`province`}
                                    autoComplete="off"
                                    name="province"
                                    placeholder={`Province Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.currentAddress.province || ''}
                                    onChange={e => onChange(e, 'province', e.target.value)}
                                    onBlur={e => onChange(e, 'province', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Postal Code"
                                    error={!!errorMsg.postCode}
                                    helperText={errorMsg.postCode}
                                    autoComplete="off"
                                    type="text"
                                    id={`postCode`}
                                    name="postCode"
                                    placeholder={`Postal Code Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.currentAddress.postCode || ''}
                                    onChange={e => onChange(e, 'postCode', e.target.value)}
                                    onBlur={e => onChange(e, 'postCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <Autocomplete
                                    id="country"
                                    options={COUNTRY_LIST}
                                    autoHighlight={true}
                                    disableClearable={true}
                                    value={state.beneCurrAddrCountry || null}
                                    blurOnSelect={true}
                                    placeholder={"Select Country"}
                                    onChange={(e, value) => onChange(e, 'country', value)}
                                    getOptionLabel={option => option.label || ''}
                                    renderInput={params => (
                                        <TextField
                                            {...params}
                                            label="Choose a country"
                                            error={!!errorMsg.country}
                                            helperText={errorMsg.country}
                                            classes={{root: "custom-text-input"}}
                                            inputProps={{
                                                ...params.inputProps
                                            }}
                                        />
                                    )}
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
