import React, {useEffect, useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {DatePicker} from "@material-ui/pickers";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step12(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [addType, setAddType] = useState("apt");

    useEffect(() => {
        if (props.state.hasOwnProperty(`prevEmployerDetails`)) {
            if (props.state.prevEmployerDetails.hasOwnProperty('apt')) setAddType('apt');
            if (props.state.prevEmployerDetails.hasOwnProperty('ste')) setAddType('ste');
            if (props.state.prevEmployerDetails.hasOwnProperty('floor')) setAddType('floor');
        }
    }, []);

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'dontHavePrevPosition':
                updatedState.dontHavePrevPosition = !updatedState.dontHavePrevPosition;
                updatedState.prevEmployerDetails = {};
                updatedState.prevEmployerCountry = {};
                errors = {};
                setErrorMsg(errors);
                break;
            case 'name':
            case 'street':
            case 'state':
            case 'zipCode':
            case 'startDate':
            case 'endDate':
            case 'occupation':
                updatedState.prevEmployerDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.prevEmployerDetails, 'required', errors).error);
                break;
            case 'city':
                updatedState.prevEmployerDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.prevEmployerDetails, 'requiredWithSpace', errors).error);
                break;
            case 'postCode':
            case 'apt':
            case 'ste':
            case 'floor':
            case 'province':
                updatedState.prevEmployerDetails[fieldName] = value;
                break;
            case 'country':
                updatedState.prevEmployerCountry = value;
                updatedState.prevEmployerDetails[fieldName] = value.code;
                setErrorMsg(fieldValidator(fieldName, updatedState.prevEmployerDetails, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.prevEmployerDetails.apt;
                delete updatedState.prevEmployerDetails.ste;
                delete updatedState.prevEmployerDetails.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        let newForm;

        if (!state.dontHavePrevPosition) {
            errors = fieldValidator('name', state.prevEmployerDetails, 'requiredChar', errors).error;
            errors = fieldValidator('street', state.prevEmployerDetails, 'required', errors).error;
            errors = fieldValidator('city', state.prevEmployerDetails, 'requiredWithSpace', errors).error;
            errors = fieldValidator('state', state.prevEmployerDetails, 'required', errors).error;
            errors = fieldValidator('zipCode', state.prevEmployerDetails, 'required', errors).error;
            if (state.prevEmployerDetails.country !== "US") {
                errors = fieldValidator('postCode', state.prevEmployerDetails, 'required', errors).error;
            }
            errors = fieldValidator('startDate', state.prevEmployerDetails, 'required', errors).error;
            errors = fieldValidator('endDate', state.prevEmployerDetails, 'required', errors).error;
            errors = fieldValidator('occupation', state.prevEmployerDetails, 'required', errors).error;
            newForm = fieldValidator('country', state.prevEmployerDetails, 'required', errors);

            if (newForm.isValid) {
                props.history.push(nextStep);
            } else {
                setErrorMsg(newForm.error)
            }
        } else {
            props.history.push(nextStep);
        }
    };

    return (
        <Step
            heading={`Because you did not have the same position over the last 5 years, I have to ask where were you  previously employed?`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        {state.hasOwnProperty('prevEmployerDetails') ? <React.Fragment>

                            <MDBRow center className={`mb-2`}>
                                <MDBFormInline className={`inline-components`}>
                                <MDBInput
                                    label="I did not have any Previous Position"
                                    id='prevPositionToggle'
                                    type='checkbox'
                                    name={`prevPositionToggle`}
                                    checked={state.dontHavePrevPosition}
                                    className={`custom-check-large`}
                                    containerClass='mr-5'
                                    onChange={e => onChange(e, "dontHavePrevPosition", true)}
                                />
                                </MDBFormInline>
                            </MDBRow>

                            {!state.dontHavePrevPosition && <React.Fragment>
                                <MDBRow center className={`mb-2`}>
                                    <MDBCol>
                                        <TextField
                                            label="Previous Employer's Name?"
                                            type="text"
                                            id={`employerName`}
                                            name="employerName"
                                            error={!!errorMsg.name}
                                            helperText={errorMsg.name}
                                            value={state.prevEmployerDetails.name || ''}
                                            classes={{root: "custom-text-input"}}
                                            placeholder={`Previous Employer Name`}
                                            onChange={e => onChange(e, 'name', e.target.value)}
                                            onBlur={e => onChange(e, 'name', e.target.value)}
                                            required
                                        />
                                    </MDBCol>
                                    <MDBCol>
                                        <TextField
                                            label="Street Number and Name"
                                            error={!!errorMsg.street}
                                            helperText={errorMsg.street}
                                            type="text"
                                            id={`street`}
                                            autoComplete="off"
                                            name="street"
                                            placeholder={`Street Details`}
                                            classes={{root: "custom-text-input"}}
                                            value={state.prevEmployerDetails.street || ''}
                                            onChange={e => onChange(e, 'street', e.target.value)}
                                            onBlur={e => onChange(e, 'street', e.target.value)}
                                            required
                                        />
                                    </MDBCol>
                                </MDBRow>

                                <MDBRow center className={`mb-2`}>
                                    <MDBCol>
                                        <MDBFormInline className={`justify-content-center`}>
                                            <MDBInput
                                                label="APT."
                                                type='checkbox'
                                                id='addTypes1'
                                                name={`addTypes1`}
                                                checked={addType === "apt"}
                                                className={`custom-check-large`}
                                                containerClass='mr-2'
                                                onChange={e => onChange(e, 'addType', 'apt')}
                                            />
                                            <MDBInput
                                                label="Suite"
                                                type='checkbox'
                                                id='addTypes2'
                                                name={`addTypes2`}
                                                checked={addType === "ste"}
                                                className={`custom-check-large`}
                                                containerClass='mr-2'
                                                onChange={e => onChange(e, 'addType', 'ste')}
                                            />
                                            <MDBInput
                                                label="Floor"
                                                type='checkbox'
                                                id='addTypes3'
                                                name={`addTypes3`}
                                                checked={addType === "floor"}
                                                className={`custom-check-large`}
                                                containerClass='mr-2'
                                                onChange={e => onChange(e, 'addType', 'floor')}
                                            />
                                        </MDBFormInline>
                                    </MDBCol>
                                    <MDBCol>
                                        <TextField
                                            label={types[addType].label}
                                            error={!!errorMsg[addType]}
                                            helperText={errorMsg[addType]}
                                            type="text"
                                            autoComplete="off"
                                            id={types[addType].id}
                                            name={types[addType].id}
                                            placeholder={types[addType].placeholder}
                                            classes={{root: "custom-text-input"}}
                                            value={state.prevEmployerDetails[addType] || ''}
                                            onChange={e => onChange(e, addType, e.target.value)}
                                            onBlur={e => onChange(e, addType, e.target.value)}
                                            required
                                        />
                                    </MDBCol>
                                </MDBRow>

                                <MDBRow center className={`mb-2`}>
                                    <MDBCol>
                                        <TextField
                                            label="City or Town"
                                            error={!!errorMsg.city}
                                            helperText={errorMsg.city}
                                            type="text"
                                            id={`employerCity`}
                                            autoComplete="off"
                                            name="employerCity"
                                            placeholder={`City`}
                                            classes={{root: "custom-text-input"}}
                                            value={state.prevEmployerDetails.city || ''}
                                            onChange={e => onChange(e, 'city', e.target.value)}
                                            onBlur={e => onChange(e, 'city', e.target.value)}
                                            required
                                        />
                                    </MDBCol>
                                    <MDBCol>
                                        <TextField
                                            label="State"
                                            error={!!errorMsg.state}
                                            helperText={errorMsg.state}
                                            type="text"
                                            id={`employerState`}
                                            autoComplete="off"
                                            name="employerState"
                                            placeholder={`State Name`}
                                            classes={{root: "custom-text-input"}}
                                            value={state.prevEmployerDetails.state || ''}
                                            onChange={e => onChange(e, 'state', e.target.value)}
                                            onBlur={e => onChange(e, 'state', e.target.value)}
                                            required
                                        />
                                    </MDBCol>
                                </MDBRow>

                                <MDBRow center className={`mb-2`}>
                                    <MDBCol>
                                        <TextField
                                            label="Zip Code"
                                            error={!!errorMsg.zipCode}
                                            helperText={errorMsg.zipCode}
                                            type="text"
                                            id={`employerZip`}
                                            name="employerZip"
                                            placeholder={`Zip Code`}
                                            classes={{root: "custom-text-input"}}
                                            value={state.prevEmployerDetails.zipCode || ''}
                                            onChange={e => onChange(e, 'zipCode', e.target.value)}
                                            onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                            required
                                        />
                                    </MDBCol>
                                    <MDBCol>
                                        <TextField
                                            label="Province"
                                            error={!!errorMsg.province}
                                            helperText={errorMsg.province}
                                            type="text"
                                            id={`employerProvince`}
                                            autoComplete="off"
                                            name="employerProvince"
                                            placeholder={`Province Details`}
                                            classes={{root: "custom-text-input"}}
                                            value={state.prevEmployerDetails.province || ''}
                                            onChange={e => onChange(e, 'province', e.target.value)}
                                            onBlur={e => onChange(e, 'province', e.target.value)}
                                        />
                                    </MDBCol>
                                </MDBRow>

                                <MDBRow center className={`mb-2`}>
                                    <MDBCol>
                                        <TextField
                                            label="Postal Code"
                                            error={!!errorMsg.postCode}
                                            helperText={errorMsg.postCode}
                                            autoComplete="off"
                                            type="text"
                                            id={`employerPostCode`}
                                            name="employerPostCode"
                                            placeholder={`Postal Code Details`}
                                            classes={{root: "custom-text-input"}}
                                            value={state.prevEmployerDetails.postCode || ''}
                                            onChange={e => onChange(e, 'postCode', e.target.value)}
                                            onBlur={e => onChange(e, 'postCode', e.target.value)}
                                        />
                                    </MDBCol>
                                    <MDBCol>
                                        <Autocomplete
                                            options={COUNTRY_LIST}
                                            autoHighlight={true}
                                            disableClearable={true}
                                            value={state.prevEmployerCountry || null}
                                            blurOnSelect={true}
                                            id="employerCountry"
                                            placeholder={"Select Country"}
                                            onChange={(e, value) => onChange(e, 'country', value)}
                                            getOptionLabel={option => option.label || ''}
                                            renderInput={params => (
                                                <TextField
                                                    {...params}
                                                    label="Country"
                                                    classes={{root: "custom-text-input"}}
                                                    error={!!errorMsg.country}
                                                    helperText={errorMsg.country}
                                                    inputProps={{
                                                        ...params.inputProps
                                                    }}
                                                />
                                            )}
                                        />
                                    </MDBCol>
                                </MDBRow>

                                <MDBRow center className={`mb-2`}>
                                    <TextField
                                        label="Previous Occupation"
                                        error={!!errorMsg.occupation}
                                        helperText={errorMsg.occupation}
                                        type="text"
                                        id={`occupation`}
                                        autoComplete="off"
                                        name="occupation"
                                        placeholder={`Your Occupation`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.prevEmployerDetails.occupation || ''}
                                        onChange={e => onChange(e, 'occupation', e.target.value)}
                                        onBlur={e => onChange(e, 'occupation', e.target.value)}
                                        required
                                    />
                                </MDBRow>

                                <MDBRow center className={`mb-2`}>
                                    <MDBCol>
                                        <DatePicker
                                            label="Joined From?"
                                            variant="inline"
                                            id={`startDate`}
                                            name="startDate"
                                            format="dd/MM/yyyy"
                                            error={!!errorMsg.startDate}
                                            helperText={errorMsg.startDate}
                                            value={state.prevEmployerDetails.startDate || null}
                                            autoOk={true}
                                            disableFuture={true}
                                            classes={{root: "custom-text-input"}}
                                            placeholder={`Date of Joining`}
                                            onChange={e => onChange(e, 'startDate', e.toLocaleDateString())}
                                        />
                                    </MDBCol>
                                    <MDBCol>
                                        <DatePicker
                                            label="End Date?"
                                            variant="inline"
                                            id={`endDate`}
                                            name="endDate"
                                            format="dd/MM/yyyy"
                                            error={!!errorMsg.endDate}
                                            helperText={errorMsg.endDate}
                                            value={state.prevEmployerDetails.endDate || null}
                                            autoOk={true}
                                            disableFuture={true}
                                            classes={{root: "custom-text-input"}}
                                            placeholder={`Job End Date`}
                                            onChange={e => onChange(e, 'endDate', e.toLocaleDateString())}
                                        />
                                    </MDBCol>
                                </MDBRow>

                                <MDBRow center className="d-flex flex-column align-items-center">
                                    {Object.keys(errorMsg).length > 0 && (
                                        <div className="text-danger mb-2">Please Fill out all the required fields</div>
                                    )}
                                    <MDBBtn
                                        color="pink"
                                        rounded
                                        type="button"
                                        className="z-depth-1a"
                                        onClick={() => onNext()}
                                    >
                                        Next
                                    </MDBBtn>
                                </MDBRow>

                            </React.Fragment>}
                        </React.Fragment> : <h4 className="text-center mb-2">
                            You have not entered Required Information to Enter Previous Employer Information
                            Please Go Back
                        </h4>}

                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
