import React, {useEffect, useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {DatePicker} from "@material-ui/pickers";
import Autocomplete from "@material-ui/lab/Autocomplete";
import {COUNTRY_LIST} from "../../shared/referenceData";

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step24(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [addType, setAddType] = useState("apt");

    const beneficiaryInfo = state.beneficiaryInfo;

    useEffect(() => {
        if (props.state.beneficiaryInfo.lastLivedInfo) {
            if (props.state.beneficiaryInfo.lastLivedInfo.hasOwnProperty('apt')) setAddType('apt');
            if (props.state.beneficiaryInfo.lastLivedInfo.hasOwnProperty('ste')) setAddType('ste');
            if (props.state.beneficiaryInfo.lastLivedInfo.hasOwnProperty('floor')) setAddType('floor');
        }
    }, []);


    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'isFormForSpouse':
                updatedState.beneficiaryInfo[fieldName] = value;
                if (value) {
                    updatedState.beneficiaryInfo['lastLivedInfo'] = {};
                } else {
                    delete updatedState.beneficiaryInfo.lastLivedInfo;
                    errors = {};
                }
                setErrorMsg(fieldValidator(fieldName, state.beneficiaryInfo, 'requiredBool', errors).error);
                break;
            case 'street':
            case 'apt':
            case 'ste':
            case 'floor':
            case 'city':
            case 'state':
            case 'zipCode':
            case 'province':
            case 'postCode':
            case 'dateOfResidingFrom':
            case 'dateOfResidingTo':
                updatedState.beneficiaryInfo.lastLivedInfo[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo.lastLivedInfo, 'required', errors).error);
                break;
            case 'country':
                updatedState.beneficiaryInfo.lastLivedInfo[fieldName] = value.code;
                updatedState['lastLivedCountry'] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo.lastLivedInfo, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.beneficiaryInfo.lastLivedInfo.apt;
                delete updatedState.beneficiaryInfo.lastLivedInfo.ste;
                delete updatedState.beneficiaryInfo.lastLivedInfo.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;

        }
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        let errors = {};
        let newForm;
        let shortState = state.beneficiaryInfo;

        newForm = fieldValidator('isFormForSpouse', shortState, 'requiredBool', errors);

        if (shortState.isFormForSpouse) {
            errors = fieldValidator('street', shortState.lastLivedInfo, 'required', newForm.error).error;
            errors = fieldValidator('city', shortState.lastLivedInfo, 'required', errors).error;
            errors = fieldValidator('state', shortState.lastLivedInfo, 'required', errors).error;
            errors = fieldValidator('zipCode', shortState.lastLivedInfo, 'required', errors).error;
            errors = fieldValidator('province', shortState.lastLivedInfo, 'required', errors).error;
            errors = fieldValidator('postCode', shortState.lastLivedInfo, 'required', errors).error;
            errors = fieldValidator('country', shortState.lastLivedInfo, 'required', errors).error;
            errors = fieldValidator('dateOfResidingFrom', shortState.lastLivedInfo, 'required', errors).error;
            errors = fieldValidator('dateOfResidingTo', shortState.lastLivedInfo, 'required', errors).error;
            newForm = fieldValidator(addType, shortState.lastLivedInfo, 'required', errors);
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step
            heading={`Is this Form for Your Spouse?`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>
                            <MDBFormInline className={`justify-content-center`}>
                                <MDBInput
                                    label="Yes"
                                    type='checkbox'
                                    id='isFormForSpouse'
                                    name={`isFormForSpouse`}
                                    checked={state.beneficiaryInfo.isFormForSpouse || false}
                                    className={`custom-check-large`}
                                    containerClass='mr-2'
                                    onChange={e => onChange(e, 'isFormForSpouse', true)}
                                />
                                <MDBInput
                                    label="No"
                                    type='checkbox'
                                    id='everBeenInProceedingsNo'
                                    name={`everBeenInProceedingsNo`}
                                    checked={state.beneficiaryInfo.isFormForSpouse === false || false}
                                    className={`custom-check-large`}
                                    containerClass='mr-2'
                                    onChange={e => onChange(e, 'isFormForSpouse', false)}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        {beneficiaryInfo.isFormForSpouse && <React.Fragment>
                            <h4 className="text-center mb-2">Please Provide the last Address you lived Together</h4>

                            <MDBRow center className={`mb-2`}>
                                <TextField
                                    label="Street Number and Name"
                                    error={!!errorMsg.street}
                                    helperText={errorMsg.street}
                                    type="text"
                                    id={`street`}
                                    autoComplete="off"
                                    name="street"
                                    placeholder={`Street Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={beneficiaryInfo.lastLivedInfo.street || ''}
                                    onChange={e => onChange(e, 'street', e.target.value)}
                                    onBlur={e => onChange(e, 'street', e.target.value)}
                                    required
                                />
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <MDBFormInline className={`justify-content-center`}>
                                        <MDBInput
                                            label="APT."
                                            type='checkbox'
                                            id='addTypes1'
                                            name={`addTypes1`}
                                            checked={addType === "apt"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'apt')}
                                        />
                                        <MDBInput
                                            label="Suite"
                                            type='checkbox'
                                            id='addTypes2'
                                            name={`addTypes2`}
                                            checked={addType === "ste"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'ste')}
                                        />
                                        <MDBInput
                                            label="Floor"
                                            type='checkbox'
                                            id='addTypes3'
                                            name={`addTypes3`}
                                            checked={addType === "floor"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'floor')}
                                        />
                                    </MDBFormInline>
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label={types[addType].label}
                                        error={!!errorMsg[addType]}
                                        helperText={errorMsg[addType]}
                                        type="text"
                                        autoComplete="off"
                                        id={types[addType].id}
                                        name={types[addType].id}
                                        placeholder={types[addType].placeholder}
                                        classes={{root: "custom-text-input"}}
                                        value={beneficiaryInfo.lastLivedInfo[addType] || ''}
                                        onChange={e => onChange(e, addType, e.target.value)}
                                        onBlur={e => onChange(e, addType, e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="City or Town"
                                        error={!!errorMsg.city}
                                        helperText={errorMsg.city}
                                        type="text"
                                        id={`livedCity`}
                                        autoComplete="off"
                                        name="livedCity"
                                        placeholder={`City`}
                                        classes={{root: "custom-text-input"}}
                                        value={beneficiaryInfo.lastLivedInfo.city || ''}
                                        onChange={e => onChange(e, 'city', e.target.value)}
                                        onBlur={e => onChange(e, 'city', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="State"
                                        error={!!errorMsg.state}
                                        helperText={errorMsg.state}
                                        type="text"
                                        id={`stateInfo`}
                                        autoComplete="off"
                                        name="stateInfo"
                                        placeholder={`State Name`}
                                        classes={{root: "custom-text-input"}}
                                        value={beneficiaryInfo.lastLivedInfo.state || ''}
                                        onChange={e => onChange(e, 'state', e.target.value)}
                                        onBlur={e => onChange(e, 'state', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Zip Code"
                                        error={!!errorMsg.zipCode}
                                        helperText={errorMsg.zipCode}
                                        type="text"
                                        id={`zipCode`}
                                        name="zipCode"
                                        placeholder={`Zip Code`}
                                        classes={{root: "custom-text-input"}}
                                        value={beneficiaryInfo.lastLivedInfo.zipCode || ''}
                                        onChange={e => onChange(e, 'zipCode', e.target.value)}
                                        onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="Province"
                                        error={!!errorMsg.province}
                                        helperText={errorMsg.province}
                                        type="text"
                                        id={`province`}
                                        autoComplete="off"
                                        name="province"
                                        placeholder={`Province Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={beneficiaryInfo.lastLivedInfo.province}
                                        onChange={e => onChange(e, 'province', e.target.value)}
                                        onBlur={e => onChange(e, 'province', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Postal Code"
                                        error={!!errorMsg.postCode}
                                        helperText={errorMsg.postCode}
                                        autoComplete="off"
                                        type="text"
                                        id={`livedPostCode`}
                                        name="livedPostCode"
                                        placeholder={`Postal Code Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={beneficiaryInfo.lastLivedInfo.postCode}
                                        onChange={e => onChange(e, 'postCode', e.target.value)}
                                        onBlur={e => onChange(e, 'postCode', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <Autocomplete
                                        id="country"
                                        options={COUNTRY_LIST}
                                        autoHighlight={true}
                                        disableClearable={true}
                                        value={state.lastLivedCountry || null}
                                        blurOnSelect={true}
                                        placeholder={"Select Country"}
                                        onChange={(e, value) => onChange(e, 'country', value)}
                                        getOptionLabel={option => option.label || ''}
                                        renderInput={params => (
                                            <TextField
                                                {...params}
                                                label="Last Lived Country"
                                                error={!!errorMsg.country}
                                                helperText={errorMsg.country}
                                                classes={{root: "custom-text-input"}}
                                                inputProps={{
                                                    ...params.inputProps
                                                }}
                                            />
                                        )}
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <DatePicker
                                        label="From?"
                                        variant="inline"
                                        id={`dateOfResidingFrom`}
                                        name={`dateOfResidingFrom`}
                                        format="dd/MM/yyyy"
                                        error={!!errorMsg.dateOfResidingFrom}
                                        helperText={errorMsg.dateOfResidingFrom}
                                        value={beneficiaryInfo.lastLivedInfo.dateOfResidingFrom || null}
                                        autoOk={true}
                                        disableFuture={true}
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`When did you started living here?`}
                                        onChange={e => onChange(e, 'dateOfResidingFrom', e.toLocaleDateString())}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <DatePicker
                                        label="To?"
                                        variant="inline"
                                        id={`dateOfResidingTo`}
                                        name={`dateOfResidingTo`}
                                        format="dd/MM/yyyy"
                                        error={!!errorMsg.dateOfResidingTo}
                                        helperText={errorMsg.dateOfResidingTo}
                                        value={beneficiaryInfo.lastLivedInfo.dateOfResidingTo || null}
                                        autoOk={true}
                                        disableFuture={true}
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`Last lived Date?`}
                                        onChange={e => onChange(e, 'dateOfResidingTo', e.toLocaleDateString())}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>
                        </React.Fragment>}


                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>

                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
