import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";

export default function Step26(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'isApplyingForImmigrantVisa':
                updatedState[fieldName] = value;
                if (value) {
                    updatedState['applyingForImmigrantVisa'] = {};
                } else {
                    delete updatedState.applyingForImmigrantVisa;
                    delete errors.city;
                    delete errors.state;
                    delete errors.province;
                    delete errors.country;
                }
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredBool', errors).error);
                break;
            case 'city':
            case 'state':
            case 'province':
                updatedState.applyingForImmigrantVisa[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.applyingForImmigrantVisa, 'required', errors).error);
                break;
            case 'country':
                updatedState.applyingForImmigrantVisa[fieldName] = value.code;
                updatedState['immigrantVisaApplyCountry'] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.applyingForImmigrantVisa, 'required', errors).error);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        let errors = {};
        let newForm;

        newForm = fieldValidator('isApplyingForImmigrantVisa', state, 'requiredBool', errors);

        if (state.isApplyingForImmigrantVisa) {
            errors = fieldValidator('city', state.applyingForImmigrantVisa, 'required', newForm.error).error;
            errors = fieldValidator('state', state.applyingForImmigrantVisa, 'required', errors).error;
            errors = fieldValidator('province', state.applyingForImmigrantVisa, 'required', errors).error;
            newForm = fieldValidator('country', state.applyingForImmigrantVisa, 'required', errors);
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step
            heading={`Does the below Statement applies to you?`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h4 className={`mb-2 text-center`}>
                            The beneficiary will not apply for adjustment of status in the United States, but he or she
                            will apply for an immigrant visa abroad at the U.S. Embassy or U.S. Consulate in
                        </h4>

                        <MDBRow center className={`mb-2`}>
                            <MDBFormInline className={`justify-content-center`}>
                                <MDBInput
                                    label="Yes"
                                    type='checkbox'
                                    id='isApplyingForImmigrantVisaYes'
                                    name={`isApplyingForImmigrantVisaYes`}
                                    checked={state.isApplyingForImmigrantVisa || false}
                                    className={`custom-check-large`}
                                    containerClass='mr-2'
                                    onChange={e => onChange(e, 'isApplyingForImmigrantVisa', true)}
                                />
                                <MDBInput
                                    label="No"
                                    type='checkbox'
                                    id='isApplyingForImmigrantVisaNo'
                                    name={`isApplyingForImmigrantVisaNo`}
                                    checked={state.isApplyingForImmigrantVisa === false || false}
                                    className={`custom-check-large`}
                                    containerClass='mr-2'
                                    onChange={e => onChange(e, 'isApplyingForImmigrantVisa', false)}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        {state.isApplyingForImmigrantVisa && <React.Fragment>
                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="City or Town"
                                        error={!!errorMsg.city}
                                        helperText={errorMsg.city}
                                        type="text"
                                        id={`city`}
                                        autoComplete="off"
                                        name={`city`}
                                        placeholder={`City`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.applyingForImmigrantVisa.city || ''}
                                        onChange={e => onChange(e, 'city', e.target.value)}
                                        onBlur={e => onChange(e, 'city', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="State"
                                        error={!!errorMsg.state}
                                        helperText={errorMsg.state}
                                        type="text"
                                        id={`stateInfo`}
                                        autoComplete="off"
                                        name="stateInfo"
                                        placeholder={`State Name`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.applyingForImmigrantVisa.state || ''}
                                        onChange={e => onChange(e, 'state', e.target.value)}
                                        onBlur={e => onChange(e, 'state', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Province"
                                        error={!!errorMsg.province}
                                        helperText={errorMsg.province}
                                        type="text"
                                        id={`province`}
                                        autoComplete="off"
                                        name="province"
                                        placeholder={`Province Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.applyingForImmigrantVisa.province || ''}
                                        onChange={e => onChange(e, 'province', e.target.value)}
                                        onBlur={e => onChange(e, 'province', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <Autocomplete
                                        id="country"
                                        options={COUNTRY_LIST}
                                        autoHighlight={true}
                                        disableClearable={true}
                                        value={state.immigrantVisaApplyCountry || null}
                                        blurOnSelect={true}
                                        placeholder={"Select Country"}
                                        onChange={(e, value) => onChange(e, 'country', value)}
                                        getOptionLabel={option => option.label || ''}
                                        renderInput={params => (
                                            <TextField
                                                {...params}
                                                label="Country"
                                                error={!!errorMsg.country}
                                                helperText={errorMsg.country}
                                                classes={{root: "custom-text-input"}}
                                                inputProps={{
                                                    ...params.inputProps
                                                }}
                                            />
                                        )}
                                    />
                                </MDBCol>
                            </MDBRow>
                        </React.Fragment>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>

                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
