import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow} from "mdbreact";
import Step from "./Step";
import {fieldValidator} from "./validator";
import {FormControlLabel} from "@material-ui/core";
import CustomCheckBox from "../../shared/utility";

export default function Step1(props) {
    const {state, nextStep} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'reqFor':
                updatedState.reqFor = value;
                delete updatedState.reqForSub;
                delete updatedState.adoptRelated;
                delete errors.reqFor;
                break;
            case 'reqForSub':
                updatedState['reqForSub'] = value;
                delete updatedState.adoptRelated;
                delete errors.reqForSub;
                break;
            case 'adoptRelated':
                updatedState['adoptRelated'] = value;
                delete updatedState.reqForSub;
                delete errors.adoptRelated;
                break;
            case 'adoptCiti':
                updatedState['adoptCiti'] = value;
                delete errors.adoptCiti;
                break;
            default:
                break;
        }
        setErrorMsg(errors);
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        let newForm;
        let errors = {};
        if (state.reqFor === "Spouse") {
            props.history.push(nextStep);
        } else {
            newForm = fieldValidator('reqFor', state, 'required');

            if (state.reqFor === "Parent" || state.reqFor === "Child") {
                newForm = fieldValidator('reqForSub', state, 'required', errors);
            }

            if (state.reqFor === "Brother") {
                newForm = fieldValidator('adoptRelated', state, 'requiredBool', newForm.error);
            }

            newForm = fieldValidator('adoptCiti', state, 'requiredBool', newForm.error);

            if (newForm.isValid) {
                props.history.push(nextStep);
            } else {
                setErrorMsg(newForm.error);
            }
        }
    };

    return (
        <Step
            heading={
                "Hello, My Name is Madison and I am here to help you."
            }
        >
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`12`}>
                        <h4 className={`mb-2 mt-2 text-center`}>Who are you completing this request for?</h4>

                        <MDBRow center>
                            <FormControlLabel
                                className={`mb-0`}
                                control={
                                    <CustomCheckBox
                                        id={`spouse`}
                                        checked={state.reqFor === "Spouse"}
                                        onChange={e => onChange(e, "reqFor", "Spouse")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Spouse</span>)}
                            />
                            <FormControlLabel
                                className={`mb-0`}
                                control={
                                    <CustomCheckBox
                                        id={`parent`}
                                        checked={state.reqFor === "Parent"}
                                        onChange={e => onChange(e, "reqFor", "Parent")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Parent</span>)}
                            />
                            <FormControlLabel
                                className={`mb-0`}
                                control={
                                    <CustomCheckBox
                                        id={`brother`}
                                        checked={state.reqFor === "Brother"}
                                        onChange={e => onChange(e, "reqFor", "Brother")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Brother/Sister</span>)}
                            />
                            <FormControlLabel
                                className={`mb-0`}
                                control={
                                    <CustomCheckBox
                                        id={`child`}
                                        checked={state.reqFor === "Child"}
                                        onChange={e => onChange(e, "reqFor", "Child")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Child</span>)}
                            />
                        </MDBRow>

                        {(state.reqFor === "Parent" || state.reqFor === "Child") && <React.Fragment>
                            <div>
                                <h3 className={`text-center`}>
                                    What best Describes your Relationship?
                                </h3>
                            </div>

                            <MDBRow center>
                                <div className={`text-center`}>
                                    <MDBRow start>
                                        <FormControlLabel
                                            className={`mb-0`}
                                            control={
                                                <CustomCheckBox
                                                    id={`parentSub1`}
                                                    checked={state.reqForSub === "sub1"}
                                                    onChange={e => onChange(e, "reqForSub", "sub1")}
                                                    color="primary"
                                                    inputProps={{'aria-label': 'secondary checkbox'}}
                                                />
                                            }
                                            label={(<span className={`labelClass`}>Child was born to parents who were married to each other at the time of the child`s birth</span>)}
                                        />
                                    </MDBRow>
                                    <MDBRow start>
                                        <FormControlLabel
                                            className={`mb-0`}
                                            control={
                                                <CustomCheckBox
                                                    id={`parentSub2`}
                                                    checked={state.reqForSub === "sub2"}
                                                    onChange={e => onChange(e, "reqForSub", "sub2")}
                                                    color="primary"
                                                    inputProps={{'aria-label': 'secondary checkbox'}}
                                                />
                                            }
                                            label={(<span className={`labelClass`}>Stepchild/Stepparent</span>)}
                                        />
                                    </MDBRow>
                                    <MDBRow start>
                                        <FormControlLabel
                                            className={`mb-0`}
                                            control={
                                                <CustomCheckBox
                                                    id={`parentSub3`}
                                                    checked={state.reqForSub === "sub3"}
                                                    onChange={e => onChange(e, "reqForSub", "sub3")}
                                                    color="primary"
                                                    inputProps={{'aria-label': 'secondary checkbox'}}
                                                />
                                            }
                                            label={(<span className={`labelClass`}>Child was born to parents who were not married to each other at the time of the child`s birth</span>)}
                                        />
                                    </MDBRow>
                                    <MDBRow start>
                                        <FormControlLabel
                                            className={`mb-0`}
                                            control={
                                                <CustomCheckBox
                                                    id={`parentSub4`}
                                                    checked={state.reqForSub === "sub4"}
                                                    onChange={e => onChange(e, "reqForSub", "sub4")}
                                                    color="primary"
                                                    inputProps={{'aria-label': 'secondary checkbox'}}
                                                />
                                            }
                                            label={(<span className={`labelClass`}>Child was adopted (not an Orphan or Hague Convention adoptee)</span>)}
                                        />
                                    </MDBRow>
                                </div>
                            </MDBRow>

                        </React.Fragment>}

                        {(state.reqFor === "Brother") && <React.Fragment>
                            <div>
                                <h3 className={`text-center`}>
                                    Are you related by Adoption?
                                </h3>
                            </div>

                            <MDBRow center>
                                <FormControlLabel
                                    className={`mb-0`}
                                    control={
                                        <CustomCheckBox
                                            id={`yes`}
                                            checked={state.adoptRelated}
                                            onChange={e => onChange(e, "adoptRelated", true)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>Yes</span>)}
                                />
                                <FormControlLabel
                                    className={`mb-0`}
                                    control={
                                        <CustomCheckBox
                                            id={`no`}
                                            checked={state.adoptRelated === false}
                                            onChange={e => onChange(e, "adoptRelated", false)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>No</span>)}
                                />
                            </MDBRow>
                        </React.Fragment>}

                        {state.reqFor && <React.Fragment>
                            <div>
                                <h3 className={`text-center`}>
                                    Did you gain resident status or citizenship through adoption?
                                </h3>
                            </div>

                            <MDBRow center>
                                <FormControlLabel
                                    className={`mb-0`}
                                    control={
                                        <CustomCheckBox
                                            id={`yesCiti1`}
                                            checked={state.adoptCiti}
                                            onChange={e => onChange(e, "adoptCiti", true)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>Yes</span>)}
                                />
                                <FormControlLabel
                                    className={`mb-0`}
                                    control={
                                        <CustomCheckBox
                                            id={`noCity2`}
                                            checked={state.adoptCiti === false}
                                            onChange={e => onChange(e, "adoptCiti", false)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>No</span>)}
                                />
                            </MDBRow>
                        </React.Fragment>}


                        <MDBRow center className="d-flex flex-column align-items-center">
                            {(Object.keys(errorMsg).length > 0) && (
                                <div className="text-danger mb-2">Please Select All Checkboxes that are displayed</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Let's Do This
                            </MDBBtn>
                        </MDBRow>

                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
