import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import {Checkbox, FormControlLabel, TextField} from '@material-ui/core';
import {withStyles} from '@material-ui/core/styles';
import { blue } from '@material-ui/core/colors';
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import CustomCheckBox from "../../shared/utility";

const Step2 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'arToggle':
                updatedState.dontHaveARN = !updatedState.dontHaveARN;
                updatedState.arNumber = '';
                delete errors.arNumber;
                setErrorMsg(errors);
                break;
            case 'arNumber':
                updatedState.arNumber = value;
                setErrorMsg(fieldValidator('arNumber', updatedState, 'text', errorMsg).error);
                break;
            case 'uscisToggle':
                updatedState.dontHaveUSCIS = !updatedState.dontHaveUSCIS;
                updatedState.uscisOAN = '';
                delete errors.uscisOAN;
                setErrorMsg(errors);
                break;
            case 'uscisOAN':
                updatedState.uscisOAN = value;
                setErrorMsg(fieldValidator('uscisOAN', updatedState, 'text', errorMsg).error);
                break;
            case 'USSSToggle':
                updatedState.dontHaveUSSS = !updatedState.dontHaveUSSS;
                updatedState.usssNumber = '';
                delete errors.usssNumber;
                setErrorMsg(errors);
                break;
            case 'usssNumber':
                updatedState.usssNumber = value;
                setErrorMsg(fieldValidator('usssNumber', updatedState, 'text', errorMsg).error);
                break;
            case 'fname':
                updatedState.fname = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredChar', errorMsg).error);
                break;
            case 'lname':
                updatedState.lname = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredChar', errorMsg).error);
                break;
            case 'mName':
                updatedState.mName = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredChar', errorMsg).error);
                break;
            case 'prevNamesToggle':
                updatedState.isPrevNames = !updatedState.isPrevNames;
                delete updatedState.prevfname;
                delete updatedState.prevlname;
                delete updatedState.prevMName;
                delete errors.prevfname;
                delete errors.prevlname;
                delete errors.prevMName;
                setErrorMsg(errors);
                break;
            case 'prevfname':
            case 'prevlname':
            case 'prevMName':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredChar', errorMsg).error);
                break;
            default:
                break;

        }
        props.onChangeState(updatedState);
    };

    const onBlurHandler = (e, fieldName, value) => {
        setErrorMsg(fieldValidator(fieldName, state, 'required', errorMsg).error);
    };

    const onNext = () => {
        let errors = fieldValidator('arNumber', state, 'text').error;
        errors = fieldValidator('usssNumber', state, 'text', errors).error;
        errors = fieldValidator('uscisOAN', state, 'text', errors).error;
        errors = fieldValidator('fname', state, 'requiredChar', errors).error;
        errors = fieldValidator('lname', state, 'requiredChar', errors).error;
        let newForm = fieldValidator('mName', state, 'requiredChar', errors);

        if (state.isPrevNames) {
            errors = fieldValidator('prevfname', state, 'requiredChar', newForm.error).error;
            errors = fieldValidator('prevlname', state, 'requiredChar', errors).error;
            newForm = fieldValidator('prevMName', state, 'requiredChar', errors);
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Information About You (Petitioner)`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`arnToggle`}
                                            checked={state.dontHaveARN}
                                            onChange={e => onChange(e, "arToggle", e.target.checked)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>I don't have Alien Registration Number</span>)}
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    error={!!errorMsg.arNumber}
                                    helperText={errorMsg.arNumber}
                                    label="Alien Registration Number"
                                    type="text"
                                    id={`arNumber`}
                                    name="arNumber"
                                    value={state.arNumber || ''}
                                    placeholder={`i.e. A-123456789`}
                                    classes={{root: "custom-text-input"}}
                                    disabled={state.dontHaveARN}
                                    onChange={e => onChange(e, 'arNumber', e.target.value)}
                                    onBlur={e => onChange(e, 'arNumber', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`uscisToggle`}
                                            checked={state.dontHaveUSCIS}
                                            onChange={e => onChange(e, "uscisToggle", e.target.checked)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>I don't have USCIS</span>)}
                                />
                            </MDBCol>
                            <MDBCol>
                                    <TextField
                                        error={!!errorMsg.uscisOAN}
                                        helperText={errorMsg.uscisOAN}
                                        label="USCIS Online Account Number"
                                        type="number"
                                        classes={{root: "custom-text-input"}}
                                        id={`uscisOAN`}
                                        name="uscisOAN"
                                        placeholder={`i.e. 123456789123`}
                                        value={state.uscisOAN || ''}
                                        disabled={state.dontHaveUSCIS}
                                        onChange={e => {
                                            onChange(e, 'uscisOAN', e.target.value);
                                        }}
                                        onBlur={e => onChange(e, 'uscisOAN', e.target.value)}
                                        required
                                    />
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`USSSToggle`}
                                            checked={state.dontHaveUSSS}
                                            onChange={e => onChange(e, "USSSToggle", e.target.checked)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>I don't have U.S. Social Security Number</span>)}
                                />
                            </MDBCol>
                            <MDBCol>
                                    <TextField
                                        label="U.S. Social Security Number"
                                        error={!!errorMsg.usssNumber}
                                        helperText={errorMsg.usssNumber}
                                        type="number"
                                        id={`usssNumber`}
                                        name="usssNumber"
                                        placeholder={`i.e. 123456789`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.usssNumber || ''}
                                        disabled={state.dontHaveUSSS}
                                        onChange={e => {
                                            onChange(e, 'usssNumber', e.target.value);
                                        }}
                                        onBlur={e => onChange(e, 'usssNumber', e.target.value)}
                                        required
                                    />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="First Name"
                                    error={!!errorMsg.fname}
                                    helperText={errorMsg.fname}
                                    type="text"
                                    id={`fname`}
                                    name="fname"
                                    placeholder={`First Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.fname || ''}
                                    onChange={e => onChange(e, 'fname', e.target.value)}
                                    onBlur={e => onChange(e, 'fname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Middle Name"
                                    error={!!errorMsg.mName}
                                    helperText={errorMsg.mName}
                                    type="text"
                                    id={`mName`}
                                    name="mName"
                                    placeholder={`Middle Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mName || ''}
                                    onChange={e => onChange(e, 'mName', e.target.value)}
                                    onBlur={e => onChange(e, 'mName', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Family Name"
                                    error={!!errorMsg.lname}
                                    helperText={errorMsg.lname}
                                    type="text"
                                    id={`lname`}
                                    name="lname"
                                    placeholder={`Last Name or Family Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.lname || ''}
                                    onChange={e => onChange(e, 'lname', e.target.value)}
                                    onBlur={e => onChange(e, 'lname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`prevNamesToggle`}
                                            checked={state.isPrevNames}
                                            onChange={e => onChange(e, "prevNamesToggle", e.target.checked)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>Have you Ever used any other Names?</span>)}
                                />
                            </MDBCol>
                        </MDBRow>

                        {state.isPrevNames && <MDBRow center className={`mb-4`}>
                            <MDBFormInline className={`inline-components`}>
                                <MDBCol md={`3`} lg={`4`}>
                                    <TextField
                                        label="Previous First Name"
                                        error={!!errorMsg.prevfname}
                                        helperText={errorMsg.prevfname}
                                        type="text"
                                        id={`prevfname`}
                                        name="prevfname"
                                        placeholder={`Previous First Name`}
                                        value={state.prevfname || ''}
                                        onChange={e => onChange(e, 'prevfname', e.target.value)}
                                        onBlur={e => onChange(e, 'prevfname', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol md={`3`} lg={`4`}>
                                    <TextField
                                        label="Previous Last Name"
                                        error={!!errorMsg.prevlname}
                                        helperText={errorMsg.prevlname}
                                        type="text"
                                        id={`prevlname`}
                                        name="prevlname"
                                        placeholder={`Previous Last Name`}
                                        value={state.prevlname || ''}
                                        onChange={e => onChange(e, 'prevlname', e.target.value)}
                                        onBlur={e => onChange(e, 'prevlname', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol md={`3`} lg={`4`}>
                                    <TextField
                                        label="Previous Middle Name"
                                        error={!!errorMsg.prevMName}
                                        helperText={errorMsg.prevMName}
                                        type="text"
                                        id={`prevMName`}
                                        name="prevMName"
                                        placeholder={`Previous Middle Name`}
                                        value={state.prevMName || ''}
                                        onChange={e => onChange(e, 'prevMName', e.target.value)}
                                        onBlur={e => onChange(e, 'prevMName', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBFormInline>
                        </MDBRow>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">
                                    Please Fill out all the details
                                </div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step2;
