import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {DatePicker} from "@material-ui/pickers";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";

export default function Step7(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'dontKnowFather':
                updatedState.dontKnowFather = !updatedState.dontKnowFather;
                delete updatedState.fathersDetails.firstName;
                delete updatedState.fathersDetails.lastName;
                delete updatedState.fathersDetails.middleName;
                delete updatedState.fathersDetails.dob;
                delete errors.firstName;
                delete errors.lastName;
                delete errors.middleName;
                delete errors.countryBorn;
                delete errors.countryOfResidence;
                delete errors.cityOfResidence;
                delete errors.dob;
                setErrorMsg(errors);
                break;
            case 'firstName':
            case 'lastName':
            case 'middleName':
                updatedState.fathersDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.fathersDetails, 'requiredChar', errors).error);
                break;
            case 'cityOfResidence':
                updatedState.fathersDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.fathersDetails, 'requiredWithSpace', errors).error);
                break;
            case 'countryBorn':
                updatedState.fathersDetails[fieldName] = value.code;
                updatedState.fathersBornCountry = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.fathersDetails, 'requiredChar', errors).error);
                break;
            case 'countryOfResidence':
                updatedState.fathersDetails[fieldName] = value.code;
                updatedState.fathersResidenceCountry = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.fathersDetails, 'requiredChar', errors).error);
                break;
            case 'dob':
                updatedState.fathersDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.fathersDetails, 'required', errors).error);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        let newForm = {isValid: true, error: {}};

        if (!state.dontKnowFather) {
            errors = fieldValidator('firstName', state.fathersDetails, 'requiredChar', errors).error;
            errors = fieldValidator('lastName', state.fathersDetails, 'requiredChar', errors).error;
            errors = fieldValidator('middleName', state.fathersDetails, 'requiredChar', errors).error;
            errors = fieldValidator('countryBorn', state.fathersDetails, 'requiredChar', errors).error;
            errors = fieldValidator('countryOfResidence', state.fathersDetails, 'requiredChar', errors).error;
            errors = fieldValidator('cityOfResidence', state.fathersDetails, 'requiredWithSpace', errors).error;
            newForm = fieldValidator('dob', state.fathersDetails, 'required', errors);
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step
            heading={`Parental Information (Father)`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>
                            <MDBFormInline className={`justify-content-center`}>
                                <MDBInput
                                    label="I did not know my Father."
                                    type='checkbox'
                                    id='dontKnowFather'
                                    name={`dontKnowFather`}
                                    checked={state.dontKnowFather}
                                    className={`custom-check-large`}
                                    containerClass='mr-5'
                                    onChange={e => {
                                        onChange(e, "dontKnowFather", e.target.checked);
                                    }}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        {!state.dontKnowFather && <React.Fragment>
                            <MDBRow center className={`mb-4`}>
                                <MDBCol lg={`4`}>
                                    <TextField
                                        error={!!errorMsg.firstName}
                                        helperText={errorMsg.firstName}
                                        label="Father's First Name"
                                        type="text"
                                        id={`firstName`}
                                        name="firstName"
                                        value={state.fathersDetails.firstName || ''}
                                        placeholder={`Mother's First Name`}
                                        onChange={e => onChange(e, 'firstName', e.target.value)}
                                        onBlur={e => onChange(e, 'firstName', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol lg={`4`}>
                                    <TextField
                                        error={!!errorMsg.middleName}
                                        helperText={errorMsg.middleName}
                                        label="Father's Middle Name"
                                        type="text"
                                        id={`middleName`}
                                        name="middleName"
                                        value={state.fathersDetails.middleName || ''}
                                        placeholder={`Father's Middle Name`}
                                        onChange={e => onChange(e, 'middleName', e.target.value)}
                                        onBlur={e => onChange(e, 'middleName', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol lg={`4`}>
                                    <TextField
                                        error={!!errorMsg.lastName}
                                        helperText={errorMsg.lastName}
                                        label="Father's Last Name"
                                        type="text"
                                        id={`lastName`}
                                        name="lastName"
                                        value={state.fathersDetails.lastName || ''}
                                        placeholder={`Mother's Last Name`}
                                        onChange={e => onChange(e, 'lastName', e.target.value)}
                                        onBlur={e => onChange(e, 'lastName', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <DatePicker
                                        variant="inline"
                                        error={!!errorMsg.dob}
                                        helperText={errorMsg.dob}
                                        label="Father's Date of Birth"
                                        id={`dob`}
                                        format="dd/MM/yyyy"
                                        name="dob"
                                        value={state.fathersDetails.dob || null}
                                        autoOk={true}
                                        disableFuture={true}
                                        classes={{root: "custom-text-input"}}
                                        onChange={e => onChange(e, 'dob', e.toLocaleDateString())}
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <Autocomplete
                                        options={COUNTRY_LIST}
                                        autoHighlight={true}
                                        disableClearable={true}
                                        value={state.fathersBornCountry || null}
                                        blurOnSelect={true}
                                        id="countryBorn"
                                        placeholder={"Select Country"}
                                        onChange={(e, value) => onChange(e, 'countryBorn', value)}
                                        getOptionLabel={option => option.label || ''}
                                        renderInput={params => (
                                            <TextField
                                                {...params}
                                                label="Country of Birth"
                                                classes={{root: "custom-text-input"}}
                                                error={!!errorMsg.countryBorn}
                                                helperText={errorMsg.countryBorn}
                                                inputProps={{
                                                    ...params.inputProps
                                                }}
                                            />
                                        )}
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="City/Town/Village of Residence"
                                        error={!!errorMsg.cityOfResidence}
                                        helperText={errorMsg.cityOfResidence}
                                        type="text"
                                        id={`cityOfResidence`}
                                        name="cityOfResidence"
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`City of Residence`}
                                        value={state.fathersDetails.cityOfResidence || ''}
                                        onChange={e => onChange(e, 'cityOfResidence', e.target.value)}
                                        onBlur={e => onChange(e, 'cityOfResidence', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <Autocomplete
                                        options={COUNTRY_LIST}
                                        autoHighlight={true}
                                        disableClearable={true}
                                        value={state.fathersResidenceCountry || null}
                                        blurOnSelect={true}
                                        id="countryOfResidence"
                                        placeholder={"Select Country"}
                                        onChange={(e, value) => onChange(e, 'countryOfResidence', value)}
                                        getOptionLabel={option => option.label || ''}
                                        renderInput={params => (
                                            <TextField
                                                {...params}
                                                label="Country of Residence"
                                                classes={{root: "custom-text-input"}}
                                                error={!!errorMsg.countryOfResidence}
                                                helperText={errorMsg.countryOfResidence}
                                                inputProps={{
                                                    ...params.inputProps
                                                }}
                                            />
                                        )}
                                    />
                                </MDBCol>
                            </MDBRow>

                        </React.Fragment>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
