import React, {useEffect, useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";
import {DatePicker} from "@material-ui/pickers";

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step22(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [addType, setAddType] = useState("apt");

    const employerInfo = state.beneficiaryInfo.employerInfo;

    useEffect(() => {
        if(props.state.beneficiaryInfo.hasOwnProperty('employerInfo')){
            if (props.state.beneficiaryInfo.employerInfo.hasOwnProperty('apt')) setAddType('apt');
            if (props.state.beneficiaryInfo.employerInfo.hasOwnProperty('ste')) setAddType('ste');
            if (props.state.beneficiaryInfo.employerInfo.hasOwnProperty('floor')) setAddType('floor');
        }
    }, []);

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'isEmployed':
                if (!value) {
                    updatedState.beneficiaryInfo.employerInfo = {
                        employerName: "unemployed"
                    };
                } else {
                    updatedState.beneficiaryInfo.employerInfo = {};
                }
                updatedState.employerCountry = {};
                updatedState.isEmployed = value;
                errors = {};
                setErrorMsg(errors);
                break;
            case 'employerName':
                updatedState.beneficiaryInfo.employerInfo[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo.employerInfo, 'requiredChar', errors).error);
                break;
            case 'street':
            case 'apt':
            case 'ste':
            case 'floor':
            case 'city':
            case 'state':
            case 'zipCode':
            case 'province':
            case 'postCode':
            case 'employmentStartDate':
                updatedState.beneficiaryInfo.employerInfo[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo.employerInfo, 'required', errors).error);
                break;
            case 'country':
                updatedState.beneficiaryInfo.employerInfo[fieldName] = value.code;
                updatedState['employerCountry'] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo.employerInfo, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.beneficiaryInfo.employerInfo.apt;
                delete updatedState.beneficiaryInfo.employerInfo.ste;
                delete updatedState.beneficiaryInfo.employerInfo.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        let errors = {};
        let newForm = {isValid: true, error: {}};
        const beneficiaryInfo = state.beneficiaryInfo;

        if (state.isEmployed) {
            errors = fieldValidator('employerName', beneficiaryInfo.employerInfo, 'requiredChar', newForm.error).error;
            errors = fieldValidator('street', beneficiaryInfo.employerInfo, 'required', errors).error;
            errors = fieldValidator('city', beneficiaryInfo.employerInfo, 'required', errors).error;
            errors = fieldValidator('state', beneficiaryInfo.employerInfo, 'required', errors).error;
            errors = fieldValidator('zipCode', beneficiaryInfo.employerInfo, 'required', errors).error;
            errors = fieldValidator('province', beneficiaryInfo.employerInfo, 'required', errors).error;
            errors = fieldValidator('postCode', beneficiaryInfo.employerInfo, 'required', errors).error;
            errors = fieldValidator('country', beneficiaryInfo.employerInfo, 'required', errors).error;
            errors = fieldValidator('employmentStartDate', beneficiaryInfo.employerInfo, 'required', errors).error;
            newForm = fieldValidator(addType, beneficiaryInfo.employerInfo, 'required', errors);
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Immigration Relative's Employment Info`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h4 className={`text-center mb-2`}>Is your immigrating relative employed?</h4>

                        <MDBRow center className={`mb-2`}>
                            <MDBFormInline className={`justify-content-center`}>
                                <MDBInput
                                    label="Yes"
                                    type='checkbox'
                                    id='isEmployedYes'
                                    name={`isEmployedYes`}
                                    checked={state.isEmployed}
                                    className={`custom-check-large`}
                                    containerClass='mr-2'
                                    onChange={e => onChange(e, 'isEmployed', true)}
                                />
                                <MDBInput
                                    label="No"
                                    type='checkbox'
                                    id='isEmployedNo'
                                    name={`isEmployedNo`}
                                    checked={state.isEmployed === false}
                                    className={`custom-check-large`}
                                    containerClass='mr-2'
                                    onChange={e => onChange(e, 'isEmployed', false)}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        {state.isEmployed && <React.Fragment>
                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Immigrating Relative's Employer Name?"
                                        type="text"
                                        id={`employerName`}
                                        name="employerName"
                                        error={!!errorMsg.employerName}
                                        helperText={errorMsg.employerName}
                                        value={employerInfo.employerName || ''}
                                        placeholder={`Employer's Name`}
                                        classes={{root: "custom-text-input"}}
                                        onChange={e => onChange(e, 'employerName', e.target.value)}
                                        onBlur={e => onChange(e, 'employerName', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="Street Number and Name"
                                        error={!!errorMsg.street}
                                        helperText={errorMsg.street}
                                        type="text"
                                        id={`street`}
                                        autoComplete="off"
                                        name="street"
                                        placeholder={`Employer Street Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={employerInfo.street || ''}
                                        onChange={e => onChange(e, 'street', e.target.value)}
                                        onBlur={e => onChange(e, 'street', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <MDBFormInline className={`justify-content-center`}>
                                        <MDBInput
                                            label="APT."
                                            type='checkbox'
                                            id='addTypes1'
                                            name={`addTypes1`}
                                            checked={addType === "apt"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'apt')}
                                        />
                                        <MDBInput
                                            label="Suite"
                                            type='checkbox'
                                            id='addTypes2'
                                            name={`addTypes2`}
                                            checked={addType === "ste"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'ste')}
                                        />
                                        <MDBInput
                                            label="Floor"
                                            type='checkbox'
                                            id='addTypes3'
                                            name={`addTypes3`}
                                            checked={addType === "floor"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'floor')}
                                        />
                                    </MDBFormInline>
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label={types[addType].label}
                                        error={!!errorMsg[addType]}
                                        helperText={errorMsg[addType]}
                                        type="text"
                                        autoComplete="off"
                                        id={types[addType].id}
                                        name={types[addType].id}
                                        placeholder={types[addType].placeholder}
                                        classes={{root: "custom-text-input"}}
                                        value={employerInfo[addType] || ''}
                                        onChange={e => onChange(e, addType, e.target.value)}
                                        onBlur={e => onChange(e, addType, e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="City or Town"
                                        error={!!errorMsg.city}
                                        helperText={errorMsg.city}
                                        type="text"
                                        id={`employerCity`}
                                        autoComplete="off"
                                        name="employerCity"
                                        placeholder={`City`}
                                        classes={{root: "custom-text-input"}}
                                        value={employerInfo.city || ''}
                                        onChange={e => onChange(e, 'city', e.target.value)}
                                        onBlur={e => onChange(e, 'city', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="State"
                                        error={!!errorMsg.state}
                                        helperText={errorMsg.state}
                                        type="text"
                                        id={`employerState`}
                                        autoComplete="off"
                                        name="employerState"
                                        placeholder={`State Name`}
                                        classes={{root: "custom-text-input"}}
                                        value={employerInfo.state || ''}
                                        onChange={e => onChange(e, 'state', e.target.value)}
                                        onBlur={e => onChange(e, 'state', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Zip Code"
                                        error={!!errorMsg.zipCode}
                                        helperText={errorMsg.zipCode}
                                        type="text"
                                        id={`employerZip`}
                                        name="employerZip"
                                        placeholder={`Zip Code`}
                                        classes={{root: "custom-text-input"}}
                                        value={employerInfo.zipCode || ''}
                                        onChange={e => onChange(e, 'zipCode', e.target.value)}
                                        onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="Province"
                                        error={!!errorMsg.province}
                                        helperText={errorMsg.province}
                                        type="text"
                                        id={`employerProvince`}
                                        autoComplete="off"
                                        name="employerProvince"
                                        placeholder={`Province Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={employerInfo.province || ''}
                                        onChange={e => onChange(e, 'province', e.target.value)}
                                        onBlur={e => onChange(e, 'province', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Postal Code"
                                        error={!!errorMsg.postCode}
                                        helperText={errorMsg.postCode}
                                        autoComplete="off"
                                        type="text"
                                        id={`employerPostCode`}
                                        name="employerPostCode"
                                        placeholder={`Postal Code Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={employerInfo.postCode || ''}
                                        onChange={e => onChange(e, 'postCode', e.target.value)}
                                        onBlur={e => onChange(e, 'postCode', e.target.value)}
                                        required
                                    />

                                </MDBCol>
                                <MDBCol>
                                    <Autocomplete
                                        id="country"
                                        options={COUNTRY_LIST}
                                        autoHighlight={true}
                                        disableClearable={true}
                                        value={state.employerCountry || null}
                                        blurOnSelect={true}
                                        getOptionSelected={(option, value) => option.code === value}
                                        placeholder={"Select Country"}
                                        onChange={(e, value) => onChange(e, 'country', value)}
                                        getOptionLabel={option => option.label || ''}
                                        renderInput={params => (
                                            <TextField
                                                {...params}
                                                label="Employer Country"
                                                error={!!errorMsg.country}
                                                helperText={errorMsg.country}
                                                classes={{root: "custom-text-input"}}
                                                inputProps={{
                                                    ...params.inputProps
                                                }}
                                            />
                                        )}
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <DatePicker
                                    label="Date Employment Began"
                                    variant="inline"
                                    id={`employmentStartDate`}
                                    name={`employmentStartDate`}
                                    format="dd/MM/yyyy"
                                    error={!!errorMsg.employmentStartDate}
                                    helperText={errorMsg.employmentStartDate}
                                    value={employerInfo.employmentStartDate || null}
                                    autoOk={true}
                                    disableFuture={true}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Employment Start Date`}
                                    onChange={e => onChange(e, 'employmentStartDate', e.toLocaleDateString())}
                                    required
                                />
                            </MDBRow>

                        </React.Fragment>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>

                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
