import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import TextField from '@material-ui/core/TextField';
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";

export default function Step14(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'arToggle':
                updatedState.beneficiaryInfo.dontHaveARN = !updatedState.beneficiaryInfo.dontHaveARN;
                updatedState.beneficiaryInfo.arNumber = '';
                delete errors.arNumber;
                setErrorMsg(errors);
                break;
            case 'arNumber':
                updatedState.beneficiaryInfo.arNumber = value;
                setErrorMsg(fieldValidator('arNumber', updatedState.beneficiaryInfo, 'text', errorMsg).error);
                break;
            case 'uscisToggle':
                updatedState.beneficiaryInfo.dontHaveUSCIS = !updatedState.beneficiaryInfo.dontHaveUSCIS;
                updatedState.beneficiaryInfo.uscisOAN = '';
                delete errors.uscisOAN;
                setErrorMsg(errors);
                break;
            case 'uscisOAN':
                updatedState.beneficiaryInfo.uscisOAN = value;
                setErrorMsg(fieldValidator('uscisOAN', updatedState.beneficiaryInfo, 'text', errorMsg).error);
                break;
            case 'USSSToggle':
                updatedState.beneficiaryInfo.dontHaveUSSS = !updatedState.beneficiaryInfo.dontHaveUSSS;
                updatedState.beneficiaryInfo.usssNumber = '';
                delete errors.usssNumber;
                setErrorMsg(errors);
                break;
            case 'usssNumber':
                updatedState.beneficiaryInfo.usssNumber = value;
                setErrorMsg(fieldValidator('usssNumber', updatedState.beneficiaryInfo, 'text', errorMsg).error);
                break;
            case 'fname':
            case 'lname':
            case 'mName':
                updatedState.beneficiaryInfo[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.beneficiaryInfo, 'requiredChar', errorMsg).error);
                break;
            default:
                break;

        }
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        let errors = fieldValidator('arNumber', state.beneficiaryInfo, 'text').error;
        errors = fieldValidator('usssNumber', state.beneficiaryInfo, 'text', errors).error;
        errors = fieldValidator('uscisOAN', state.beneficiaryInfo, 'text', errors).error;
        errors = fieldValidator('fname', state.beneficiaryInfo, 'requiredChar', errors).error;
        errors = fieldValidator('lname', state.beneficiaryInfo, 'requiredChar', errors).error;
        let newForm = fieldValidator('mName', state.beneficiaryInfo, 'requiredChar', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Information about beneficiary`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <MDBFormInline className={`inline-components`}>
                                    <MDBInput
                                        label="Beneficiary don't have Alien Registration Number"
                                        type='checkbox'
                                        id='arnToggle'
                                        name={`arToggle`}
                                        labelClass={`custom-check-label`}
                                        checked={state.beneficiaryInfo.dontHaveARN}
                                        className={`custom-check-large`}
                                        containerClass='mr-5'
                                        onChange={e => onChange(e, "arToggle", e.target.checked)}
                                    />
                                    <TextField
                                        error={!!errorMsg.arNumber}
                                        helperText={errorMsg.arNumber}
                                        label="Alien Registration Number"
                                        type="text"
                                        id={`arNumber`}
                                        name="arNumber"
                                        value={state.beneficiaryInfo.arNumber || ''}
                                        placeholder={`i.e. A-123456789`}
                                        classes={{root: "custom-text-input"}}
                                        disabled={state.beneficiaryInfo.dontHaveARN}
                                        onChange={e => onChange(e, 'arNumber', e.target.value)}
                                        onBlur={e => onChange(e, 'arNumber', e.target.value)}
                                        required
                                    />
                                </MDBFormInline>
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <MDBFormInline className={`inline-components`}>
                                    <MDBInput
                                        label="Beneficiary don't have USCIS"
                                        type='checkbox'
                                        id='uscisToggle'
                                        name={`uscisToggle`}
                                        checked={state.beneficiaryInfo.dontHaveUSCIS}
                                        labelClass={`custom-check-label`}
                                        className={`custom-check-large`}
                                        containerClass='mr-5'
                                        onChange={e => onChange(e, "uscisToggle", e.target.checked)}
                                    />
                                    <TextField
                                        error={!!errorMsg.uscisOAN}
                                        helperText={errorMsg.uscisOAN}
                                        label="USCIS Online Account Number"
                                        type="number"
                                        classes={{root: "custom-text-input"}}
                                        id={`uscisOAN`}
                                        name="uscisOAN"
                                        placeholder={`i.e. 123456789123`}
                                        value={state.beneficiaryInfo.uscisOAN || ''}
                                        disabled={state.beneficiaryInfo.dontHaveUSCIS}
                                        onChange={e => onChange(e, 'uscisOAN', e.target.value)}
                                        onBlur={e => onChange(e, 'uscisOAN', e.target.value)}
                                        required
                                    />
                                </MDBFormInline>
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <MDBFormInline className={`inline-components`}>
                                    <MDBInput
                                        label="Beneficiary don't have U.S. Social Security Number"
                                        type='checkbox'
                                        id='USSSToggle'
                                        name={`USSSToggle`}
                                        labelClass={`custom-check-label`}
                                        checked={state.beneficiaryInfo.dontHaveUSSS}
                                        className={`custom-check-large`}
                                        containerClass='mr-5'
                                        onChange={e => onChange(e, "USSSToggle", e.target.checked)}
                                    />
                                    <TextField
                                        label="U.S. Social Security Number"
                                        error={!!errorMsg.usssNumber}
                                        helperText={errorMsg.usssNumber}
                                        type="number"
                                        id={`usssNumber`}
                                        name="usssNumber"
                                        placeholder={`i.e. 123456789`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.beneficiaryInfo.usssNumber || ''}
                                        disabled={state.beneficiaryInfo.dontHaveUSSS}
                                        onChange={e => onChange(e, 'usssNumber', e.target.value)}
                                        onBlur={e => onChange(e, 'usssNumber', e.target.value)}
                                        required
                                    />
                                </MDBFormInline>
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="First Name"
                                    error={!!errorMsg.fname}
                                    helperText={errorMsg.fname}
                                    type="text"
                                    id={`fname`}
                                    name="fname"
                                    placeholder={`First Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.fname || ''}
                                    onChange={e => onChange(e, 'fname', e.target.value)}
                                    onBlur={e => onChange(e, 'fname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Family Name"
                                    error={!!errorMsg.lname}
                                    helperText={errorMsg.lname}
                                    type="text"
                                    id={`lname`}
                                    name="lname"
                                    placeholder={`Last Name or Family Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.beneficiaryInfo.lname || ''}
                                    onChange={e => onChange(e, 'lname', e.target.value)}
                                    onBlur={e => onChange(e, 'lname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <TextField
                                label="Middle Name"
                                error={!!errorMsg.mName}
                                helperText={errorMsg.mName}
                                type="text"
                                id={`mName`}
                                name="mName"
                                placeholder={`Middle Name`}
                                classes={{root: "custom-text-input"}}
                                value={state.beneficiaryInfo.mName || ''}
                                onChange={e => onChange(e, 'mName', e.target.value)}
                                onBlur={e => onChange(e, 'mName', e.target.value)}
                                required
                            />
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">
                                    Please Fill out all the details
                                </div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
