import React, {useEffect, useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step4(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [addType, setAddType] = useState("apt");

    useEffect(() => {
        if(props.state.mailAddress){
            if (props.state.mailAddress.hasOwnProperty('apt')) setAddType('apt');
            if (props.state.mailAddress.hasOwnProperty('ste')) setAddType('ste');
            if (props.state.mailAddress.hasOwnProperty('floor')) setAddType('floor');
        }
    }, []);

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'name':
                updatedState.mailAddress['name'] = value;
                setErrorMsg(fieldValidator('name', updatedState.mailAddress, 'requiredWithSpace', errors).error);
                break;
            case 'street':
            case 'state':
            case 'zipCode':
            case 'postCode':
                updatedState.mailAddress[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.mailAddress, 'required', errors).error);
                break;
            case 'city':
                updatedState.mailAddress[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.mailAddress, 'requiredWithSpace', errors).error);
                break;
            case 'apt':
            case 'ste':
            case 'floor':
            case 'province':
                updatedState.mailAddress[fieldName] = value;
                break;
            case 'country':
                updatedState.mailAddress[fieldName] = value.code;
                updatedState.mailAddressCountry = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.mailAddress, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.mailAddress.apt;
                delete updatedState.mailAddress.ste;
                delete updatedState.mailAddress.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        errors = fieldValidator('name', state.mailAddress, 'requiredWithSpace', errors).error;
        errors = fieldValidator('street', state.mailAddress, 'required', errors).error;
        errors = fieldValidator('city', state.mailAddress, 'requiredWithSpace', errors).error;
        errors = fieldValidator('state', state.mailAddress, 'required', errors).error;
        errors = fieldValidator('zipCode', state.mailAddress, 'required', errors).error;
        errors = fieldValidator('postCode', state.mailAddress, 'required', errors).error;
        let newForm = fieldValidator('country', state.mailAddress, 'required', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`What is your Mailing Address`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="In Care of Name"
                                    error={!!errorMsg.name}
                                    helperText={errorMsg.name}
                                    type="text"
                                    autoComplete="off"
                                    id={`mailName`}
                                    name="mailName"
                                    placeholder={`Mail Address Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress.name || ''}
                                    onChange={e => onChange(e, 'name', e.target.value)}
                                    onBlur={e => onChange(e, 'name', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Street Number and Name"
                                    error={!!errorMsg.street}
                                    helperText={errorMsg.street}
                                    type="text"
                                    id={`street`}
                                    autoComplete="off"
                                    name="street"
                                    placeholder={`Street Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress.street || ''}
                                    onChange={e => onChange(e, 'street', e.target.value)}
                                    onBlur={e => onChange(e, 'street', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <MDBFormInline className={`justify-content-center`}>
                                    <MDBInput
                                        label="APT."
                                        type='checkbox'
                                        id='addTypes1'
                                        name={`addTypes1`}
                                        checked={addType === "apt"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'apt')}
                                    />
                                    <MDBInput
                                        label="Suite"
                                        type='checkbox'
                                        id='addTypes2'
                                        name={`addTypes2`}
                                        checked={addType === "ste"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'ste')}
                                    />
                                    <MDBInput
                                        label="Floor"
                                        type='checkbox'
                                        id='addTypes3'
                                        name={`addTypes3`}
                                        checked={addType === "floor"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'floor')}
                                    />
                                </MDBFormInline>
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label={types[addType].label}
                                    error={!!errorMsg[addType]}
                                    helperText={errorMsg[addType]}
                                    type="text"
                                    autoComplete="off"
                                    id={types[addType].id}
                                    name={types[addType].id}
                                    placeholder={types[addType].placeholder}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress[addType] || ''}
                                    onChange={e => onChange(e, addType, e.target.value)}
                                    onBlur={e => onChange(e, addType, e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="City or Town"
                                    error={!!errorMsg.city}
                                    helperText={errorMsg.city}
                                    type="text"
                                    id={`mailCity`}
                                    autoComplete="off"
                                    name="mailCity"
                                    placeholder={`City`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress.city || ''}
                                    onChange={e => onChange(e, 'city', e.target.value)}
                                    onBlur={e => onChange(e, 'city', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="State"
                                    error={!!errorMsg.state}
                                    helperText={errorMsg.state}
                                    type="text"
                                    id={`mailState`}
                                    autoComplete="off"
                                    name="mailState"
                                    placeholder={`State Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress.state || ''}
                                    onChange={e => onChange(e, 'state', e.target.value)}
                                    onBlur={e => onChange(e, 'state', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Zip Code"
                                    error={!!errorMsg.zipCode}
                                    helperText={errorMsg.zipCode}
                                    type="text"
                                    id={`mailZip`}
                                    name="mailZip"
                                    placeholder={`Zip Code`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress.zipCode || ''}
                                    onChange={e => onChange(e, 'zipCode', e.target.value)}
                                    onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Province"
                                    error={!!errorMsg.province}
                                    helperText={errorMsg.province}
                                    type="text"
                                    id={`mailProvince`}
                                    autoComplete="off"
                                    name="mailProvince"
                                    placeholder={`Province Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress.province || ''}
                                    onChange={e => onChange(e, 'province', e.target.value)}
                                    onBlur={e => onChange(e, 'province', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Postal Code"
                                    error={!!errorMsg.postCode}
                                    helperText={errorMsg.postCode}
                                    autoComplete="off"
                                    type="text"
                                    id={`mailPostCode`}
                                    name="mailPostCode"
                                    placeholder={`Postal Code Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mailAddress.postCode || ''}
                                    onChange={e => onChange(e, 'postCode', e.target.value)}
                                    onBlur={e => onChange(e, 'postCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <Autocomplete
                                    id="country"
                                    options={COUNTRY_LIST}
                                    autoHighlight={true}
                                    disableClearable={true}
                                    value={state.mailAddressCountry || null}
                                    blurOnSelect={true}
                                    placeholder={"Select Country"}
                                    onChange={(e, value) => onChange(e, 'country', value)}
                                    getOptionLabel={option => option.label || ''}
                                    renderInput={params => (
                                        <TextField
                                            {...params}
                                            label="Mail Address Country"
                                            classes={{root: "custom-text-input"}}
                                            error={!!errorMsg.country}
                                            helperText={errorMsg.country}
                                            inputProps={{
                                                ...params.inputProps
                                            }}
                                        />
                                    )}
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
