import React, {useEffect, useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";
import {DatePicker} from "@material-ui/pickers";

/*const StyledTextField = styled(TextField)`
  label.Mui-focused {
    color: #ff0083;
  }
  .MuiInput-underline:before {
    border-bottom: 1px solid rgb(195, 195, 195);
  }
  && .MuiInput-underline:hover:before {
    border-bottom: 2px solid #ff0083;
  }
  .MuiInput-underline:after {
    border-bottom: 2px solid #ff0083;
  }
`;*/

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step5(props) {
    const {state, prevStep, nextStep, history} = props;

    const [errorMsg, setErrorMsg] = useState({});
    const [isSame, setIsSame] = useState(false);
    const [addType, setAddType] = useState("apt");

    useEffect(() => {
        if(props.state.physAddress){
            if (props.state.physAddress.hasOwnProperty('apt')) setAddType('apt');
            if (props.state.physAddress.hasOwnProperty('ste')) setAddType('ste');
            if (props.state.physAddress.hasOwnProperty('floor')) setAddType('floor');
        }
    }, []);

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'isSame':
                if (value) {
                    updatedState.physAddress = {...updatedState.mailAddress};
                    updatedState.physAddressCountry = {...updatedState.mailAddressCountry};
                    if (updatedState.physAddress.hasOwnProperty('apt')) setAddType('apt');
                    if (updatedState.physAddress.hasOwnProperty('ste')) setAddType('ste');
                    if (updatedState.physAddress.hasOwnProperty('floor')) setAddType('floor');
                    setErrorMsg({});
                } else {
                    updatedState.physAddress = {};
                    updatedState.physAddressCountry = {};
                }
                setIsSame(!isSame);
                break;
            case 'street':
            case 'state':
            case 'zipCode':
            case 'postCode':
            case 'dateFrom':
                updatedState.physAddress[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.physAddress, 'required', errors).error);
                break;
            case 'city':
                updatedState.physAddress[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.physAddress, 'requiredWithCity', errors).error);
                break;
            case 'apt':
            case 'ste':
            case 'floor':
            case 'province':
                updatedState.physAddress[fieldName] = value;
                break;
            case 'country':
                updatedState.physAddress[fieldName] = value.code;
                updatedState.physAddressCountry = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.physAddress, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.physAddress.apt;
                delete updatedState.physAddress.ste;
                delete updatedState.physAddress.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        if(!isSame){
            errors = fieldValidator('street', state.physAddress, 'required', errors).error;
            errors = fieldValidator('city', state.physAddress, 'requiredWithSpace', errors).error;
            errors = fieldValidator('state', state.physAddress, 'required', errors).error;
            errors = fieldValidator('zipCode', state.physAddress, 'required', errors).error;
            errors = fieldValidator('dateFrom', state.physAddress, 'required', errors).error;
            let newForm = fieldValidator('country', state.physAddress, 'required', errors);
            let diff = new Date() - new Date(state.physAddress.dateFrom);
            let diffYears = Math.floor(Math.round((diff) / (1000 * 60 * 60 * 24)) / 365);

            if (newForm.isValid) {
                if (diffYears < 5) {
                    props.history.push('/start/6');
                } else {
                    props.history.push('/start/7');
                }
            } else {
                setErrorMsg(newForm.error);
            }
        } else {
            props.history.push('/start/7');
        }
    };

    return (
        <Step heading={`What is your Physical Address?`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h4 className={`text-center`}>Is you Physical address different than your mailing address? </h4>
                        <MDBRow center className={`mb-2`}>
                            <MDBFormInline className={`justify-content-center`}>
                                <MDBInput
                                    label="Yes It's Same"
                                    type='checkbox'
                                    id='physSamePhyYes'
                                    name={`physSamePhyYes`}
                                    checked={isSame}
                                    className={`custom-check-large`}
                                    containerClass='mr-2'
                                    onChange={e => onChange(e, 'isSame', true)}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <MDBFormInline className={`justify-content-center`}>
                                    <MDBInput
                                        label="APT."
                                        type='checkbox'
                                        id='addTypes1'
                                        name={`addTypes1`}
                                        disabled={isSame}
                                        checked={addType === "apt"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'apt')}
                                    />
                                    <MDBInput
                                        label="Suite"
                                        type='checkbox'
                                        id='addTypes2'
                                        name={`addTypes2`}
                                        disabled={isSame}
                                        checked={addType === "ste"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'ste')}
                                    />
                                    <MDBInput
                                        label="Floor"
                                        type='checkbox'
                                        id='addTypes3'
                                        name={`addTypes3`}
                                        disabled={isSame}
                                        checked={addType === "floor"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'floor')}
                                    />
                                </MDBFormInline>
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label={types[addType].label}
                                    error={!!errorMsg[addType]}
                                    helperText={errorMsg[addType]}
                                    type="text"
                                    disabled={isSame}
                                    autoComplete="off"
                                    id={types[addType].id}
                                    name={types[addType].id}
                                    placeholder={types[addType].placeholder}
                                    classes={{root: "custom-text-input"}}
                                    value={state.physAddress[addType] || ''}
                                    onChange={e => onChange(e, addType, e.target.value)}
                                    onBlur={e => onChange(e, addType, e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Street Number and Name"
                                    error={!!errorMsg.street}
                                    helperText={errorMsg.street}
                                    autoComplete="off"
                                    type="text"
                                    className={`custom-class-mui`}
                                    id={`street`}
                                    name="street"
                                    disabled={isSame}
                                    placeholder={`Street Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.physAddress.street || ''}
                                    onChange={e => onChange(e, 'street', e.target.value)}
                                    onBlur={e => onChange(e, 'street', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="City or Town"
                                    error={!!errorMsg.city}
                                    helperText={errorMsg.city}
                                    autoComplete="off"
                                    type="text"
                                    id={`physCity`}
                                    disabled={isSame}
                                    name="physCity"
                                    placeholder={`City`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.physAddress.city || ''}
                                    onChange={e => onChange(e, 'city', e.target.value)}
                                    onBlur={e => onChange(e, 'city', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="State"
                                    error={!!errorMsg.state}
                                    helperText={errorMsg.state}
                                    autoComplete="off"
                                    type="text"
                                    id={`physState`}
                                    name="physState"
                                    disabled={isSame}
                                    placeholder={`State Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.physAddress.state || ''}
                                    onChange={e => onChange(e, 'state', e.target.value)}
                                    onBlur={e => onChange(e, 'state', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Zip Code"
                                    error={!!errorMsg.zipCode}
                                    helperText={errorMsg.zipCode}
                                    autoComplete="off"
                                    type="text"
                                    id={`physZip`}
                                    name="physZip"
                                    disabled={isSame}
                                    placeholder={`Zip Code`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.physAddress.zipCode || ''}
                                    onChange={e => onChange(e, 'zipCode', e.target.value)}
                                    onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Province"
                                    error={!!errorMsg.province}
                                    helperText={errorMsg.province}
                                    autoComplete="off"
                                    type="text"
                                    id={`physProvince`}
                                    name="physProvince"
                                    disabled={isSame}
                                    placeholder={`Province Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.physAddress.province || ''}
                                    onChange={e => onChange(e, 'province', e.target.value)}
                                    onBlur={e => onChange(e, 'province', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Postal Code"
                                    error={!!errorMsg.postCode}
                                    helperText={errorMsg.postCode}
                                    autoComplete="off"
                                    type="text"
                                    id={`physPostCode`}
                                    name="physPostCode"
                                    disabled={isSame}
                                    placeholder={`Postal Code Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.physAddress.postCode || ''}
                                    onChange={e => onChange(e, 'postCode', e.target.value)}
                                    onBlur={e => onChange(e, 'postCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <Autocomplete
                                    id="country"
                                    options={COUNTRY_LIST}
                                    autoHighlight={true}
                                    disableClearable={true}
                                    value={state.physAddressCountry || {}}
                                    blurOnSelect={true}
                                    disabled={isSame}
                                    placeholder={"Select Country"}
                                    onChange={(e, value) => onChange(e, 'country', value)}
                                    getOptionLabel={option => option.label || ''}
                                    renderInput={params => (
                                        <TextField
                                            {...params}
                                            label="Physical Address Country"
                                            classes={{root: "custom-text-input"}}
                                            error={!!errorMsg.country}
                                            helperText={errorMsg.country}
                                            inputProps={{
                                                ...params.inputProps
                                            }}
                                        />
                                    )}
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <DatePicker
                                    label="Date From?"
                                    variant="inline"
                                    id="physAddrFrom"
                                    name={`physAddrFrom`}
                                    disableFuture={true}
                                    format="dd/MM/yyyy"
                                    error={!!errorMsg.dateFrom}
                                    disabled={isSame}
                                    helperText={errorMsg.dateFrom}
                                    value={state.physAddress.dateFrom || null}
                                    autoOk={true}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`When did you move to this address?`}
                                    onChange={e => onChange(e, 'dateFrom', e.toLocaleDateString())}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <DatePicker
                                    label="Date To (Present)"
                                    variant="inline"
                                    id="physAddrTo"
                                    name={`physAddrTo`}
                                    format="dd/MM/yyyy"
                                    autoOk={true}
                                    disabled={true}
                                    classes={{root: "custom-text-input"}}
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
