import 'date-fns';
import React, {useEffect, useState} from "react";
import {DatePicker} from '@material-ui/pickers';
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step6(props) {
    const {state, prevStep, nextStep, history} = props;

    const [errorMsg, setErrorMsg] = useState({});
    const [addType, setAddType] = useState("apt");

    useEffect(() => {
        if (props.state.physAddress.hasOwnProperty('apt')) setAddType('apt');
        if (props.state.physAddress.hasOwnProperty('ste')) setAddType('ste');
        if (props.state.physAddress.hasOwnProperty('floor')) setAddType('floor');
    }, []);

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'street':
            case 'state':
            case 'zipCode':
            case 'postCode':
            case 'dateFrom':
            case 'dateTo':
                updatedState.prevPhysAddr[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.prevPhysAddr, 'required', errors).error);
                break;
            case 'city':
                updatedState.prevPhysAddr[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.prevPhysAddr, 'requiredWithSpace', errors).error);
                break;
            case 'apt':
            case 'ste':
            case 'floor':
            case 'province':
                updatedState.prevPhysAddr[fieldName] = value;
                break;
            case 'country':
                updatedState.prevPhysAddrCountry = value;
                updatedState.prevPhysAddr[fieldName] = value.code;
                setErrorMsg(fieldValidator(fieldName, updatedState.prevPhysAddr, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.prevPhysAddr.apt;
                delete updatedState.prevPhysAddr.ste;
                delete updatedState.prevPhysAddr.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        errors = fieldValidator('street', state.prevPhysAddr, 'required', errors).error;
        errors = fieldValidator('city', state.prevPhysAddr, 'requiredWithSpace', errors).error;
        errors = fieldValidator('state', state.prevPhysAddr, 'required', errors).error;
        errors = fieldValidator('zipCode', state.prevPhysAddr, 'required', errors).error;
        errors = fieldValidator('country', state.prevPhysAddr, 'required', errors).error;
        errors = fieldValidator('postCode', state.prevPhysAddr, 'required', errors).error;
        errors = fieldValidator('dateFrom', state.prevPhysAddr, 'required', errors).error;
        errors = fieldValidator('dateTo', state.prevPhysAddr, 'required', errors).error;
        let newForm = fieldValidator('country', state.prevPhysAddr, 'required', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Because you have moved within the last 5 years, I have to ask where did you live previously? `}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <MDBFormInline className={`justify-content-center`}>
                                    <MDBInput
                                        label="APT."
                                        type='checkbox'
                                        id='prevPhysAddTypes1'
                                        name={`prevPhysAddTypes1`}
                                        checked={addType === "apt"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'apt')}
                                    />
                                    <MDBInput
                                        label="Suite"
                                        type='checkbox'
                                        id='prevPhysAddTypes2'
                                        name={`prevPhysAddTypes2`}
                                        checked={addType === "ste"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'ste')}
                                    />
                                    <MDBInput
                                        label="Floor"
                                        type='checkbox'
                                        id='prevPhysAddTypes3'
                                        name={`prevPhysAddTypes3`}
                                        checked={addType === "floor"}
                                        className={`custom-check-large`}
                                        containerClass='mr-2'
                                        onChange={e => onChange(e, 'addType', 'floor')}
                                    />
                                </MDBFormInline>
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label={types[addType].label}
                                    error={!!errorMsg[addType]}
                                    helperText={errorMsg[addType]}
                                    type="text"
                                    autoComplete="off"
                                    id={types[addType].id}
                                    name={types[addType].id}
                                    placeholder={types[addType].placeholder}
                                    classes={{root: "custom-text-input"}}
                                    value={state.prevPhysAddr[addType] || ''}
                                    onChange={e => onChange(e, addType, e.target.value)}
                                    onBlur={e => onChange(e, addType, e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Street Number and Name"
                                    error={!!errorMsg.street}
                                    helperText={errorMsg.street}
                                    autoComplete="off"
                                    type="text"
                                    className={`custom-class-mui`}
                                    id={`street`}
                                    name="street"
                                    placeholder={`Street Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.prevPhysAddr.street || ''}
                                    onChange={e => onChange(e, 'street', e.target.value)}
                                    onBlur={e => onChange(e, 'street', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="City or Town"
                                    error={!!errorMsg.city}
                                    helperText={errorMsg.city}
                                    autoComplete="off"
                                    type="text"
                                    id={`prevPhysCity`}
                                    name="prevPhysCity"
                                    placeholder={`City`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.prevPhysAddr.city || ''}
                                    onChange={e => onChange(e, 'city', e.target.value)}
                                    onBlur={e => onChange(e, 'city', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="State"
                                    error={!!errorMsg.state}
                                    helperText={errorMsg.state}
                                    autoComplete="off"
                                    type="text"
                                    id={`prevPhysState`}
                                    name="prevPhysState"
                                    placeholder={`State Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.prevPhysAddr.state || ''}
                                    onChange={e => onChange(e, 'state', e.target.value)}
                                    onBlur={e => onChange(e, 'state', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Zip Code"
                                    error={!!errorMsg.zipCode}
                                    helperText={errorMsg.zipCode}
                                    autoComplete="off"
                                    type="text"
                                    id={`prevPhysZip`}
                                    name="prevPhysZip"
                                    placeholder={`Zip Code`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.prevPhysAddr.zipCode || ''}
                                    onChange={e => onChange(e, 'zipCode', e.target.value)}
                                    onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Province"
                                    error={!!errorMsg.province}
                                    helperText={errorMsg.province}
                                    autoComplete="off"
                                    type="text"
                                    id={`prevPhysProvince`}
                                    name="prevPhysProvince"
                                    placeholder={`Province Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.prevPhysAddr.province || ''}
                                    onChange={e => onChange(e, 'province', e.target.value)}
                                    onBlur={e => onChange(e, 'province', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Postal Code"
                                    error={!!errorMsg.postCode}
                                    helperText={errorMsg.postCode}
                                    autoComplete="off"
                                    type="text"
                                    id={`physPostCode`}
                                    name="physPostCode"
                                    placeholder={`Postal Code Details`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.prevPhysAddr.postCode || ''}
                                    onChange={e => onChange(e, 'postCode', e.target.value)}
                                    onBlur={e => onChange(e, 'postCode', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <Autocomplete
                                    options={COUNTRY_LIST}
                                    autoHighlight={true}
                                    disableClearable={true}
                                    value={state.prevPhysAddrCountry || null}
                                    blurOnSelect={true}
                                    id="country"
                                    placeholder={"Select Country"}
                                    onChange={(e, value) => onChange(e, 'country', value)}
                                    getOptionLabel={option => option.label || ''}
                                    renderInput={params => (
                                        <TextField
                                            {...params}
                                            label="Previous Physical Address Country"
                                            classes={{root: "custom-text-input"}}
                                            error={!!errorMsg.country}
                                            helperText={errorMsg.country}
                                            inputProps={{
                                                ...params.inputProps
                                            }}
                                        />
                                    )}
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <DatePicker
                                    label="Date From?"
                                    variant="inline"
                                    id="physAddrFrom"
                                    name={`physAddrFrom`}
                                    disableFuture={true}
                                    format="dd/MM/yyyy"
                                    error={!!errorMsg.dateFrom}
                                    helperText={errorMsg.dateFrom}
                                    value={state.prevPhysAddr.dateFrom || null}
                                    autoOk={true}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`When did you move to this address?`}
                                    onChange={e => onChange(e, 'dateFrom', e.toLocaleDateString())}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <DatePicker
                                    label="Date To?"
                                    variant="inline"
                                    id="physAddrTo"
                                    name={`physAddrTo`}
                                    format="dd/MM/yyyy"
                                    error={!!errorMsg.dateTo}
                                    helperText={errorMsg.dateTo}
                                    value={state.prevPhysAddr.dateTo || null}
                                    autoOk={true}
                                    disableFuture={true}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`When did you leave from this address?`}
                                    onChange={e => onChange(e, 'dateTo', e.toLocaleDateString())}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
