import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {DatePicker} from "@material-ui/pickers";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";

export default function Step3(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'city':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredWithSpace', errorMsg).error);
                break;
            case 'country':
                updatedState.userBornCountryObj = value;
                updatedState[fieldName] = value.code;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errorMsg).error);
                break;
            case 'dateOfBirth':
                updatedState.dateOfBirth = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errorMsg).error);
                break;
            case 'gender':
                updatedState.gender = value;
                setErrorMsg(fieldValidator('gender', updatedState, 'required', errors).error);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        errors = fieldValidator('city', state, 'requiredWithSpace', errors).error;
        errors = fieldValidator('dateOfBirth', state, 'required', errors).error;
        errors = fieldValidator('gender', state, 'required', errors).error;
        let newForm = fieldValidator('country', state, 'required', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }

    };

    return (
        <Step
            heading={`Great to meet you ${state.fname}! Let us know some more information about you!`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol md={`10`} lg={`8`}>

                        <MDBRow center className={`mb-3`}>
                            <MDBCol>
                                <TextField
                                    label="What city were you born in?"
                                    error={!!errorMsg.city}
                                    helperText={errorMsg.city}
                                    type="text"
                                    id={`city`}
                                    name="city"
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Born City`}
                                    value={state.city || ''}
                                    onChange={e => onChange(e, 'city', e.target.value)}
                                    onBlur={e => onChange(e, 'city', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <Autocomplete
                                    id="country"
                                    options={COUNTRY_LIST}
                                    autoHighlight={true}
                                    disableClearable={true}
                                    value={state.userBornCountryObj || null}
                                    blurOnSelect={true}
                                    placeholder={"Born Country"}
                                    onChange={(e, value) => onChange(e, 'country', value)}
                                    getOptionLabel={option => option.label || ''}
                                    renderInput={params => (
                                        <TextField
                                            {...params}
                                            label="What Country were you born in?"
                                            classes={{root: "custom-text-input"}}
                                            error={!!errorMsg.country}
                                            helperText={errorMsg.country}
                                            inputProps={{
                                                ...params.inputProps
                                            }}
                                        />
                                    )}
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-3`}>
                            <MDBCol>
                                <DatePicker
                                    id="dob"
                                    label="Date of Birth"
                                    variant="inline"
                                    name={`dob`}
                                    disableFuture={true}
                                    format="dd/MM/yyyy"
                                    error={!!errorMsg.dateOfBirth}
                                    helperText={errorMsg.dateOfBirth}
                                    value={state.dateOfBirth || null}
                                    autoOk={true}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Date of Birth`}
                                    onChange={e => onChange(e, 'dateOfBirth', e.toLocaleDateString())}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <h4 className={`text-center`}>Gender</h4>
                                <MDBRow center>
                                    <MDBFormInline>
                                        <MDBCol>
                                            <MDBInput
                                                label='Male'
                                                type='checkbox'
                                                id='genderMale'
                                                name={`genderMale`}
                                                checked={state.gender === "Male"}
                                                className={`custom-check-large`}
                                                containerClass={`ml-2`}
                                                onChange={e => {
                                                    if (e.target.checked) {
                                                        onChange(e, "gender", "Male");
                                                    }
                                                }}
                                                required
                                            />
                                        </MDBCol>
                                        <MDBCol>
                                            <MDBInput
                                                label='Female'
                                                type='checkbox'
                                                id='genderFemale'
                                                name={`genderFemale`}
                                                checked={state.gender === "Female"}
                                                className={`custom-check-large`}
                                                containerClass={`ml-2`}
                                                onChange={e => {
                                                    if (e.target.checked) {
                                                        onChange(e, "gender", "Female");
                                                    }
                                                }}
                                                required
                                            />
                                        </MDBCol>
                                    </MDBFormInline>
                                </MDBRow>
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
