import React, {useEffect, useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBFormInline, MDBInput, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {DatePicker} from "@material-ui/pickers";
import {COUNTRY_LIST} from "../../shared/referenceData";
import Autocomplete from "@material-ui/lab/Autocomplete";

const types = {
    apt: {
        label: "Apartment",
        id: "apt",
        placeholder: "Apartment Details"
    },
    ste: {
        label: "Suite",
        id: "ste",
        placeholder: "Suite Details"
    },
    floor: {
        label: "Floor",
        id: "floor",
        placeholder: "Floor Details"
    }
}

export default function Step7(props) {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [addType, setAddType] = useState("apt");

    useEffect(() => {
        if(props.state.employerDetails){
            if (props.state.employerDetails.hasOwnProperty('apt')) setAddType('apt');
            if (props.state.employerDetails.hasOwnProperty('ste')) setAddType('ste');
            if (props.state.employerDetails.hasOwnProperty('floor')) setAddType('floor');
        }
    }, []);

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'employed':
                updatedState[fieldName] = value;
                if (value) {
                    updatedState['employerDetails'] = {};
                } else {
                    delete updatedState.employerDetails;
                    errors = {};
                }
                setErrorMsg(errors);
                break;
            case 'name':
            case 'street':
            case 'state':
            case 'zipCode':
            case 'startDate':
            case 'occupation':
                updatedState.employerDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.employerDetails, 'required', errors).error);
                break;
            case 'postCode':
                updatedState.employerDetails[fieldName] = value;
                break;
            case 'city':
                updatedState.employerDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.employerDetails, 'requiredWithSpace', errors).error);
                break;
            case 'apt':
            case 'ste':
            case 'floor':
            case 'province':
                updatedState.employerDetails[fieldName] = value;
                break;
            case 'country':
                updatedState.employerInfoCountry = value;
                updatedState.employerDetails[fieldName] = value.code;
                setErrorMsg(fieldValidator(fieldName, updatedState.employerDetails, 'required', errors).error);
                break;
            case 'addType':
                delete updatedState.employerDetails.apt;
                delete updatedState.employerDetails.ste;
                delete updatedState.employerDetails.floor;
                delete errors.apt;
                delete errors.ste;
                delete errors.floor;
                setAddType(value);
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        let newForm;

        if (state.employed) {
            errors = fieldValidator('name', state.employerDetails, 'requiredChar', errors).error;
            errors = fieldValidator('street', state.employerDetails, 'required', errors).error;
            errors = fieldValidator('city', state.employerDetails, 'requiredWithSpace', errors).error;
            errors = fieldValidator('state', state.employerDetails, 'required', errors).error;
            errors = fieldValidator('zipCode', state.employerDetails, 'required', errors).error;
            if(state.employerDetails.country !== "US"){
                errors = fieldValidator('postCode', state.employerDetails, 'required', errors).error;
            }
            errors = fieldValidator('startDate', state.employerDetails, 'required', errors).error;
            errors = fieldValidator('occupation', state.employerDetails, 'required', errors).error;
            newForm = fieldValidator('country', state.employerDetails, 'required', errors);

            let diff = new Date() - new Date(state.employerDetails.startDate);
            let diffYears = Math.floor(Math.round((diff) / (1000 * 60 * 60 * 24)) / 365);
            if (diffYears < 5) {
                if(!state.hasOwnProperty(`prevEmployerDetails`)){
                    props.onChangeState({...state, prevEmployerDetails: {}});
                }
            }

            if (newForm.isValid) {
                if (diffYears < 5) {
                    props.history.push(nextStep);
                } else {
                    props.history.push('/start/13');
                }
            } else {
                setErrorMsg(newForm.error);
            }
        } else {
            props.history.push('/start/13');
        }
    };

    return (
        <Step
            heading={`Employment Details`}
        >
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h4 className={`text-center mb-2`}>Have you been employed in last 5 years?</h4>

                        <MDBRow center className={`mb-2`}>
                            <MDBFormInline className={`justify-content-center`}>
                                <MDBInput
                                    label="Yes"
                                    type='checkbox'
                                    id='employedYes'
                                    name={`employedYes`}
                                    className={`custom-check-large`}
                                    checked={state.employed}
                                    containerClass='mr-3'
                                    onChange={e => onChange(e, 'employed', true)}
                                />
                                <MDBInput
                                    label="No"
                                    type='checkbox'
                                    id='employedNo'
                                    name={`employedNo`}
                                    className={`custom-check-large`}
                                    checked={state.employed === false}
                                    containerClass='mr-3'
                                    onChange={e => onChange(e, 'employed', false)}
                                />
                            </MDBFormInline>
                        </MDBRow>

                        {state.employed && <React.Fragment>
                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="What is your Employer's Name?"
                                        type="text"
                                        id={`employerName`}
                                        name="employerName"
                                        error={!!errorMsg.name}
                                        helperText={errorMsg.name}
                                        value={state.employerDetails.name || ''}
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`Employer Name`}
                                        onChange={e => onChange(e, 'name', e.target.value)}
                                        onBlur={e => onChange(e, 'name', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="Street Number and Name"
                                        error={!!errorMsg.street}
                                        helperText={errorMsg.street}
                                        type="text"
                                        id={`street`}
                                        autoComplete="off"
                                        name="street"
                                        placeholder={`Street Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.employerDetails.street || ''}
                                        onChange={e => onChange(e, 'street', e.target.value)}
                                        onBlur={e => onChange(e, 'street', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <MDBFormInline className={`justify-content-center`}>
                                        <MDBInput
                                            label="APT."
                                            type='checkbox'
                                            id='addTypes1'
                                            name={`addTypes1`}
                                            checked={addType === "apt"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'apt')}
                                        />
                                        <MDBInput
                                            label="Suite"
                                            type='checkbox'
                                            id='addTypes2'
                                            name={`addTypes2`}
                                            checked={addType === "ste"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'ste')}
                                        />
                                        <MDBInput
                                            label="Floor"
                                            type='checkbox'
                                            id='addTypes3'
                                            name={`addTypes3`}
                                            checked={addType === "floor"}
                                            className={`custom-check-large`}
                                            containerClass='mr-2'
                                            onChange={e => onChange(e, 'addType', 'floor')}
                                        />
                                    </MDBFormInline>
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label={types[addType].label}
                                        error={!!errorMsg[addType]}
                                        helperText={errorMsg[addType]}
                                        type="text"
                                        autoComplete="off"
                                        id={types[addType].id}
                                        name={types[addType].id}
                                        placeholder={types[addType].placeholder}
                                        classes={{root: "custom-text-input"}}
                                        value={state.employerDetails[addType] || ''}
                                        onChange={e => onChange(e, addType, e.target.value)}
                                        onBlur={e => onChange(e, addType, e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="City or Town"
                                        error={!!errorMsg.city}
                                        helperText={errorMsg.city}
                                        type="text"
                                        id={`employerCity`}
                                        autoComplete="off"
                                        name="employerCity"
                                        placeholder={`City`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.employerDetails.city || ''}
                                        onChange={e => onChange(e, 'city', e.target.value)}
                                        onBlur={e => onChange(e, 'city', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="State"
                                        error={!!errorMsg.state}
                                        helperText={errorMsg.state}
                                        type="text"
                                        id={`employerState`}
                                        autoComplete="off"
                                        name="employerState"
                                        placeholder={`State Name`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.employerDetails.state || ''}
                                        onChange={e => onChange(e, 'state', e.target.value)}
                                        onBlur={e => onChange(e, 'state', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Zip Code"
                                        error={!!errorMsg.zipCode}
                                        helperText={errorMsg.zipCode}
                                        type="text"
                                        id={`employerZip`}
                                        name="employerZip"
                                        placeholder={`Zip Code`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.employerDetails.zipCode || ''}
                                        onChange={e => onChange(e, 'zipCode', e.target.value)}
                                        onBlur={e => onChange(e, 'zipCode', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <TextField
                                        label="Province"
                                        error={!!errorMsg.province}
                                        helperText={errorMsg.province}
                                        type="text"
                                        id={`employerProvince`}
                                        autoComplete="off"
                                        name="employerProvince"
                                        placeholder={`Province Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.employerDetails.province || ''}
                                        onChange={e => onChange(e, 'province', e.target.value)}
                                        onBlur={e => onChange(e, 'province', e.target.value)}
                                        required
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <TextField
                                        label="Postal Code"
                                        error={!!errorMsg.postCode}
                                        helperText={errorMsg.postCode}
                                        autoComplete="off"
                                        type="text"
                                        id={`employerPostCode`}
                                        name="employerPostCode"
                                        placeholder={`Postal Code Details`}
                                        classes={{root: "custom-text-input"}}
                                        value={state.employerDetails.postCode || ''}
                                        onChange={e => onChange(e, 'postCode', e.target.value)}
                                        onBlur={e => onChange(e, 'postCode', e.target.value)}
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <Autocomplete
                                        options={COUNTRY_LIST}
                                        autoHighlight={true}
                                        disableClearable={true}
                                        value={state.employerInfoCountry || null}
                                        blurOnSelect={true}
                                        id="employerCountry"
                                        placeholder={"Select Country"}
                                        onChange={(e, value) => onChange(e, 'country', value)}
                                        getOptionLabel={option => option.label || ''}
                                        renderInput={params => (
                                            <TextField
                                                {...params}
                                                label="Country"
                                                classes={{root: "custom-text-input"}}
                                                error={!!errorMsg.country}
                                                helperText={errorMsg.country}
                                                inputProps={{
                                                    ...params.inputProps
                                                }}
                                            />
                                        )}
                                    />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <TextField
                                    label="Occupation"
                                    error={!!errorMsg.occupation}
                                    helperText={errorMsg.occupation}
                                    type="text"
                                    id={`occupation`}
                                    autoComplete="off"
                                    name="occupation"
                                    placeholder={`Your Occupation`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.employerDetails.occupation || ''}
                                    onChange={e => onChange(e, 'occupation', e.target.value)}
                                    onBlur={e => onChange(e, 'occupation', e.target.value)}
                                    required
                                />
                            </MDBRow>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    <DatePicker
                                        label="Joined From?"
                                        variant="inline"
                                        id={`startDate`}
                                        name="startDate"
                                        format="dd/MM/yyyy"
                                        error={!!errorMsg.startDate}
                                        helperText={errorMsg.startDate}
                                        value={state.employerDetails.startDate || null}
                                        autoOk={true}
                                        disableFuture={true}
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`Date of Joining`}
                                        onChange={e => onChange(e, 'startDate', e.toLocaleDateString())}
                                    />
                                </MDBCol>
                                <MDBCol>
                                    <DatePicker
                                        label="End Date? (Present)"
                                        variant="inline"
                                        id={`endDate`}
                                        name="endDate"
                                        format="dd/MM/yyyy"
                                        classes={{root: "custom-text-input"}}
                                        disabled={true}
                                    />
                                </MDBCol>
                            </MDBRow>
                        </React.Fragment>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}
